import { one2many } from "../Helpers";
import { d2t, notFinite2t } from "../Helpers.convert";
import { randFrom } from "../Helpers.rand";
function makeDFmt() {
    function toDFmt(sFmt) {
        switch (sFmt.toLocaleLowerCase()) {
            case "d": return ":d";
            case "f": return ":f";
            case "w": return ":w";
            case "r": return ":r";
            default: return ":d";
        }
    }
    function parseFromValue(d) {
        let fmt = ":d";
        let hasPoint = d.includes('.');
        let hasSlash = d.includes('/');
        let hasSpace = d.trim().includes(' ');
        if (hasSlash && hasSpace)
            fmt = ":w";
        else if (hasSlash)
            fmt = ":f";
        else if (hasPoint)
            fmt = ":d";
        return fmt;
    }
    function parseFromStr(str) {
        let [_, right] = str.splitTrim(':');
        let fmt = right ? toDFmt(right) : ":d";
        return fmt;
    }
    let _priority = {
        ":d": 1,
        ":f": 10,
        ":w": 100,
        ":r": 1000,
    };
    function parse(first, ...others) {
        let implicitFmt = ":d";
        let explicitFmt;
        let updateImplicit = fmt => implicitFmt =
            !implicitFmt ? fmt :
                _priority[fmt] >= _priority[implicitFmt] ? fmt :
                    implicitFmt;
        let updateExplicit = fmt => explicitFmt =
            !explicitFmt ? fmt :
                _priority[fmt] >= _priority[explicitFmt] ? fmt :
                    explicitFmt;
        for (let value of [...one2many(first), ...others]) {
            if (value.includes(':')) {
                let fmt = parseFromStr(value);
                updateExplicit(fmt);
            }
            else {
                let fmt = parseFromValue(value);
                updateImplicit(fmt);
            }
        }
        return explicitFmt ? explicitFmt : implicitFmt;
    }
    function format(d, fmt) {
        if (!d.isFinite())
            return notFinite2t(d);
        let isFraction = !d.toFraction()[1].eq(1);
        let isFixed = d.minus(d.toDP(3)).eq(0);
        let fixed = d.toString();
        let mightBeWhole = d.gt(1);
        if (fmt === ":r") {
            let variants = [];
            if (isFraction)
                variants.push(d2t(d, false));
            if (isFraction && mightBeWhole)
                variants.push(d2t(d, true));
            if (isFixed)
                variants.push(fixed);
            return randFrom(variants);
        }
        switch (fmt) {
            case ":d": return isFixed ? fixed : d2t(d, false);
            case ":w": return d2t(d);
            case ":f": return d2t(d, false);
            default:
                const _ = fmt;
                break;
        }
    }
    function formats(many, fmt) {
        return many.map(x => format(x, fmt));
    }
    return {
        toDFmt,
        parse,
        format,
        formats,
    };
}
;
export const DFmt = makeDFmt();
