<script lang="ts">
    import { writable } from "svelte/store";
    import type { Sheet } from "./Sq.Cell";

    const tmpDivUnique = "__//__";

    let toAscii = (value: string) =>
        value.length > 0 ? '`' + value + '`' : "";

    let getFmt = (text: string) => text
        .replaceAll("-:", tmpDivUnique)
        .split(":")[1];

    function getValue(text: string) {
        if (!text) return "";

        text = text.replaceAll("-:", tmpDivUnique);
        let [value, _] = text.split(":");
        value = value.replaceAll(tmpDivUnique, "-:");

        return value;
    }

    function getBgColor(text: string, $default: string) {
        let fmt = getFmt(text);

        do {
            if (!fmt) break;
            if (fmt.includes("g")) return "bg-green-100";
            if (fmt.includes("y")) return "bg-yellow-100";
            if (fmt.includes("r")) return "bg-red-100";
            if (fmt.includes("b")) return "bg-blue-100";
            if (fmt.includes("w")) return "bg-white";
        } while(false);

        return $default;
    }

    function getTextSize(text: string, $default: string) {
        let fmt = getFmt(text);
        let textDiff = options.get("text-diff") ?? "text-xs";

        do {
            if (!fmt) break;
            if (fmt.includes("1")) return textDiff;
            if (fmt.includes("2")) return textDiff;
        } while (false);

        return $default;
    }

    function recreateUiIfNeeded(states: string[], stateNo: number) {
        if (getValue(states[0]) === getValue(states[1]))
            return;
        recreate.set({});
    }

    export let cell: Sheet[0][0];

    let {name, states, stateNo, options} = cell;
    let recreate = writable({});

    let defaultTextSize = options.get("text-seq") ?? "text-xl";

    $: state = states[$stateNo] ?? "";
    $: value = getValue(state);
    $: bgColor = getBgColor(state, "bg-white");
    $: textSize = getTextSize(state, defaultTextSize);
    $: recreateUiIfNeeded(states, $stateNo);
</script>

<!-- svelte-ignore a11y-click-events-have-key-events -->
{#key $recreate}
<span
    class="
        w-full h-full
        text-center
        {textSize} {bgColor}
    "
    style="grid-area:{name};"
    on:click
>
    {toAscii(value)}
</span>
{/key}

<style>
    span {
        display: flex;
        justify-content: center;
        align-items: center;
    }    
</style>