<script lang="ts">

    import Grid from './../core/Grid.svelte'
    import Clock from './../core/Clock.svelte'
    import { range } from '../core/Helpers';
    import { randFrom } from '../core/Helpers.rand';

    import {GcdLcm as self} from './GcdLcm.svelte'
    import {on, fire} from './Model.svelte'
    import Go from "./Go.svelte"
    import Num from "./Num.svelte"
    import Factor from "./Factor.svelte"

    import Count from './Count.svelte'
    import Congrats from '../core/Congrats.svelte';

    let congrats: Congrats;

    function done() {
        congrats.play();
        self.done();
    }

    on("--cmd-new-round", () => self.newRound());
    on("--evt-done", () => done());
    on("--cmd-idle", () => self.idle());
    fire("--cmd-new-round");

    let layout = {
        areas: `
            "l1"
            "l2"
            "l3"
            "l4"
            "go"
        `,
        cols: `1fr`,
        rows: `60px 80px 150px 150px 80px`
    };

    let l1 = {
        areas: `
            "cnt . clk"
        `,
        cols: `2fr 1fr 2fr`,
        rows: `1fr`
    };

    let l2 = {
        areas: `
            ". lhs rhs ."
        `,
        cols: `1fr 1fr 1fr 1fr`,
        rows: `1fr`
    };

    let l3 = {
        areas: `
            "0 1 2 3"
            "4 5 6 7"
        `,
        cols: `1fr 1fr 1fr 1fr`,
        rows: `1fr 1fr`
    };

    let l4 = {
        areas: `
            "gcd lcm"
        `,
        cols: `1fr 1fr`,
        rows: `1fr`
    };

    let gcd = {
        areas: `
            "d1 d2"
            "d3 d4"
        `,
        cols: `1fr 1fr`,
        rows: `1fr 1fr`
    };
    
    let lcm = {
        areas: `
            "m1 m2"
            "m3 m4"
        `,
        cols: `1fr 1fr`,
        rows: `1fr 1fr`
    };
    
    let view = {
        count: <Count> null,
        nums: new Array<Num>(2),
        factors: new Array<Factor>(8),
        gcd: new Array<Num>(4),
        lcm: new Array<Num>(4)
    }

    on("--evt-go", () => {
        randFrom(self.game.divisors).wrong = true;
        fire("--cmd-update-views");
    });

    on("--cmd-set-models", game => {
        view.count.setModel(game.roundsLeft);
        view.nums[0].setModel(game.nums[0]);
        view.nums[1].setModel(game.nums[1]);
        game.divisors.forEach((m, i) => view.factors[i].setModel(m));
    })

</script>

<Grid {layout}>
    <Grid layout={l1} ga="l1">
        <Count ga="cnt" bind:this={view.count} />
        <Clock ga="clk" />
    </Grid>

    <Grid layout={l2} ga="l2">
        <Num ga=lhs bind:this={view.nums[0]}/>
        <Num ga=rhs bind:this={view.nums[1]}/>
    </Grid>

    <Grid layout={l3} ga="l3">
        {#each range(0, 8) as no}
            <Factor ga={no}, bind:this={view.factors[no]} />
        {/each}
    </Grid>

    <!--Grid layout={l4} ga="l4">
        <Grid layout={gcd} ga="gcd">
            {#each range(0, 4) as no}
                <Num ga={no.toString()} />
            {/each}
        </Grid>

        <Grid layout={lcm} ga="lcm">
            {#each range(0, 4) as no}
                <Num ga={no.toString()} />
            {/each}
        </Grid>
    </Grid-->

    <Go ga=go/>

    <Congrats bind:this={congrats} />
</Grid>
