import { DFmt } from "./formatters/decimal-format";
import { t2d, d2t, notFinite2t, d2n } from "./Helpers.convert";
import { randFrom, shuffle } from "./Helpers.rand";
import { assert, _l, throwError } from "./Logger";
export function newCellAsRandomGenerator(cell, lnNo = -1) {
    function parseOperation(def) {
        return def.startsWith("*|") ?
            ["*", def.slice(2)] :
            ["+", def];
    }
    function enumerateRange(rangeDef) {
        let [op, rangeNoOp] = parseOperation(rangeDef);
        let [rangeWithStep, sFmt] = rangeNoOp.split(":");
        let [range, sStep] = rangeWithStep.split("|");
        let [sFirst, sEnd] = range.split("..");
        sStep ??= "1";
        let fmt = sFmt ? DFmt.toDFmt(sFmt) : DFmt.parse(sFirst, sEnd, sStep);
        let [first, end, step] = [
            t2d(sFirst), t2d(sEnd), t2d(sStep)
        ];
        let values = [];
        for (let it = first; it.lte(end); it = it.add(step)) {
            values.push({ d: it, fmt, op });
        }
        return values;
    }
    function enumerateSet(set) {
        let [op, setNoOp] = parseOperation(set);
        let tokens = setNoOp.splitTrimNoEmpties(',');
        let values = [];
        tokens.forEach(token => {
            let [num, sFmt] = token.split(':');
            let fmt = sFmt ? DFmt.toDFmt(sFmt) : undefined;
            let d = t2d(num);
            values.push({ asIs: num, d, fmt, op });
        });
        return values;
    }
    function format(value) {
        let { asIs, d, fmt } = value;
        if (asIs && !fmt)
            return asIs;
        assert(d && fmt, x => _l(x));
        if (!d.isFinite())
            return notFinite2t(d);
        let isFraction = !d.toFraction()[1].eq(1);
        let isFixed = d.minus(d.toDP(2)).eq(0);
        let fixed = d.toString();
        let mightBeWhole = d.gt(1);
        if (asIs && fmt === ":r") {
            let variants = [asIs];
            if (isFraction)
                variants.push(d2t(d, false));
            if (isFraction && mightBeWhole)
                variants.push(d2t(d, true));
            if (isFixed)
                variants.push(fixed);
            return randFrom(variants);
        }
        if (fmt === ":r")
            fmt = randFrom([":d", ":w", ":f"]);
        switch (fmt) {
            case ":d": return isFixed ? fixed : d2t(d);
            case ":w": return d2t(d);
            case ":f": return d2t(d, false);
            default:
                const _ = fmt;
                break;
        }
    }
    let defs = cell.splitTrim(';');
    if (defs.includes(""))
        throwError(`Empty line in cell random generator; cell='${cell}'`, x => _l(x));
    if (lnNo >= 0) { // if specified take only one row
        defs = [defs[lnNo % defs.length]];
    }
    let _values = defs
        .map(def => def.includes("..") ?
        enumerateRange(def) :
        enumerateSet(def))
        .flat();
    if (_values.length === 0)
        throwError(`Cell random generator produced no numbers; cell='${cell}'`, x => _l(x));
    return {
        op: _values[0].op,
        all: () => d2n(_values.map(x => x.d)),
        one: () => format(randFrom(_values)),
        many: (count) => {
            if (count <= 0)
                throwError(`Can't take ${count} numbers`, x => _l(x));
            let nums = new Set();
            for (let value of shuffle(_values)) {
                nums.add(format(value));
                if (nums.size === count)
                    break;
            }
            return [...nums];
        }
    };
}
