<script context="module" lang="ts">

    import { handleDragEvents } from './DragHandler.svelte';
    import type { EventName, DragArgs } from './DragHandler.svelte';
    import type { Mp3Name } from "./Mp3.svelte"
    import { Mp3 } from './Mp3.svelte';


    interface BounceOptions {
        id?: string;
        snapTo?: [number, number];
    }

    type DragEvent = CustomEvent<DragArgs>;
    type Point = DragArgs["start"];

    export function bounceOnDrag(elem: HTMLElement, options?: BounceOptions) {

        options ??= {}
        options.snapTo ??= [5, 5];

        function setTranslate(pnt: Point) {
            elem.style.transform = `translate3d(${pnt.x}px, ${pnt.y}px, 0px)`;
        }

        function removeTranslation() {
            elem.style.transform = null;
        }

        function bounceBack(from: Point) {

            let back = [
                { transform: `translate3d(0px, 0px, 0px)` },
            ];

            elem.animate(back, {
                duration: 100,
                easing: "cubic-bezier(.87,.74,.96,1.83)"
            })
            .onfinish = () => removeTranslation();
        }

        function onDragStart(ev: DragEvent) {
            Mp3.play("on-drag-start");
        }

        function onDragMove(ev: DragEvent) {
            setTranslate(ev.detail.offset);
        }

        function onDragEnd(ev: DragEvent) {
            Mp3.play("on-drag-end");
            bounceBack(ev.detail.offset)
        }

        elem.addEventListener("drag:start", onDragStart);
        elem.addEventListener("drag:move", onDragMove);
        elem.addEventListener("drag:end", onDragEnd);

        let drag = handleDragEvents(elem, options);

        return {
            destroy() {
                drag.destroy();
                elem.removeEventListener("drag:start", onDragStart);
                elem.removeEventListener("drag:move", onDragMove);
                elem.removeEventListener("drag:end", onDragEnd);
            }
        }
    }

    type SoundMap = Map<EventName, Mp3Name>;

    export function soundsOnDrag(elem: HTMLElement, map?: SoundMap) {

        map ??= new Map();

        function onDragStart(ev: DragEvent) {
            Mp3.play(map.get("drag:start"));
        }

        function onDragEnd(ev: DragEvent) {
            Mp3.play(map.get("drag:end"));
        }

        elem.addEventListener("drag:start", onDragStart);
        elem.addEventListener("drag:end", onDragEnd);

        let drag = handleDragEvents(elem);

        return {
            destroy() {
                drag.destroy();
                elem.removeEventListener("drag:start", onDragStart);
                elem.removeEventListener("drag:end", onDragEnd);
            }
        }

    }

</script>