import { DFmt } from "../core/formatters/decimal-format";
import { one2many, range } from "../core/Helpers";
import { n2d } from "../core/Helpers.convert";
import { randFrom } from "../core/Helpers.rand";
import { assert } from "../core/Logger";
let _uniqueId = 0;
export function newVariable({ name, view, value, fmt }) {
    let uniqueName = `__${++_uniqueId}__`;
    let v_name = `~${name}`;
    let v_uniqueName = `~${uniqueName}`;
    let _view = one2many(view);
    let _value = one2many(value);
    function variateValues(count) {
        if (_view.length > 1)
            return;
        assert(_value.length === 1);
        let base = _value[0];
        let abs = _value[0].abs();
        let step = n2d(abs.lte(1) ? 0.1 :
            abs.lte(2) ? 0.2 :
                abs.lte(3) ? 0.5 :
                    abs.lte(10) ? 1 :
                        abs.lte(50) ? 5 : 10);
        range(count).forEach(n => {
            let x = step.mul(n + 1);
            let v1 = base.minus(x);
            let v2 = base.plus(x);
            _value.push(v1, v2);
            _view.push(DFmt.format(v1, fmt), DFmt.format(v2, fmt));
        });
    }
    variateValues(3);
    function toUniqueName(str) {
        return str
            .replaceAll(v_name, v_uniqueName)
            .replaceAll(name, uniqueName);
    }
    function toView({ str, variate = false }) {
        let [value, v_value] = [_view[0], _view[0]];
        if (variate && _view.length > 1) {
            v_value = randFrom(_view.slice(1));
        }
        return str
            .replaceAll(v_uniqueName, v_value)
            .replaceAll(uniqueName, value);
    }
    function iAmHere(str) {
        let exact = str.includes(name) ||
            str.includes(uniqueName);
        let variant = str.includes(v_name) ||
            str.includes(v_uniqueName);
        return exact && !variant;
    }
    return {
        name,
        view: _view[0],
        value: _value[0],
        uniqueName,
        values: _value,
        views: _view,
        toUniqueName,
        toView,
        rand: () => randFrom(_view),
        iAmHere,
    };
}
