<script lang="ts">
    import { onMount } from "svelte";

    import Progress from "../core/Progress2.svelte"
    import Congrats from "../core/Congrats.svelte";
    import Grid from "../core/Grid.svelte";
    import { makeProgress } from "../core/Progress2";
    import Timer from "../core/Timer.svelte";

    import { applyAsciiMath } from "../core/AsciiMathUtils";
    import { makeSqDefinition } from "./Sq.Definition";
    import { makeSqModel } from "./Sq.Model";
    import { makeSqStore } from "./Sq.Store";

    import Sheet from "./Sq.Sheet.svelte"
    import Ans from "./Sq.Ans.svelte"
    import { log, _l } from "../core/Logger";

    let solved = false;
    let itsDone = false;

    let congrats: Congrats;
    let timer: Timer;

    onMount(() => {
        applyAsciiMath(1000);
    });

    let definition = log.onerror(
        () => makeSqDefinition(),
        ex => _l(ex));

//    _l(definition);

    let model = log.onerror(
        () => makeSqModel(definition),
        ex => _l(ex));

    let store = log.onerror(
        () => makeSqStore(model),
        ex => _l(ex));

    let progress = makeProgress({
        doneCount: model.settings.rounds,
        onDone: () => done()
    });

    let hasProgress = progress.isOn;
    let hasTimer = definition.settings.timer > 0;

    $: stateNo = store.stateNo;

    function done() {
        congrats.play();
        itsDone = true;
    }

    function timeIsUp() {
        progress.addWork();
        timer.restart();
    }

    function anyClick() {
        if (!solved)
            return;

        switchStateNo();
    }

    function newSequence() {
        model = makeSqModel(definition);
        store = makeSqStore(model);
        solved = false;
        stateNo.set(0);

        if (itsDone)
            progress.addWork();

        applyAsciiMath(200);
    }

    function gridClick() {
        if (!solved)
            return;

        newSequence();
    }

    function switchStateNo() {
        stateNo.update(no => no === 0 ? 1 : 0);
        applyAsciiMath(100);
    }

    function autoNext() {
        let autoNextTimeout = store.options.v2i("auto-next");

        if (!autoNextTimeout)
            return;

        setTimeout(() => gridClick(), autoNextTimeout);
    }

    function rightClick() {
        progress.rightClick();
        autoNext();
    }

    function wrongClick() {
        progress.wrongClick();
    }

    function answerIsClicked(no: number) {
        if (solved) {
            newSequence();
            return;
        }

        store.answers[no].isRight ?
            rightClick() : wrongClick();

        solved = true;

        store.answers.forEach((ans, i) => 
            ans.isClicked.set(store.answers[i].isRight));

        store.answers[no].isClicked.set(true);

        switchStateNo();
    }

    let layout = {
        areas: `
            "prg  prg  prg"
            "sh   sh   sh "
            "a0    .   a1 "
            "a2    .   a3 "
            "tm   tm   tm "
        `,
        cols: `2fr 1fr 2fr`,
        rows: `10px 10fr 2fr 2fr 10px`
    };
</script>


<Grid
    on:click={() => gridClick()}
    {layout}
>
    <Congrats bind:this={congrats} />

    {#if $hasProgress}
        <Progress ga="prg" store={progress} />
    {/if}

    {#key store}
        <Sheet on:click={() => anyClick()}
            ga="sh" {store}
        />

        {#each store.answers as ans, no}
            <Ans store = {ans}
                ga={`a${no}`} 
                on:click={() => answerIsClicked(no)}
            />
        {/each}
    {/key}

    {#if hasTimer}
        <Timer ga="tm" seconds={definition.settings.timer}
            bind:this={timer}
            onExpiration={() => timeIsUp()}
        />
    {/if}
</Grid>