import { assert, log, _l } from "../core/Logger";
import { one2many } from "../core/Helpers";
function newFormulas() {
    let _factories = new Map();
    function registerFactory(nameOrMany, factory) {
        let names = one2many(nameOrMany);
        names.forEach(name => {
            assert(!_factories.has(name), `Formula '${name}' is already registered`);
            _factories.set(name, factory);
        });
    }
    let makeFormula = from => {
        let name = from.args["formula"].splitTrim('|')[0];
        let factory = _factories.get(name);
        assert(factory, `No formula '${name}' is registered`);
        let formula = log.onerror(() => factory(from), ex => _l(ex, `While making '${name}'`));
        return formula;
    };
    return {
        registerFactory,
        makeFormula
    };
}
export const Formulas = newFormulas();
