export function makeCell({ row, col, content, canBeQuestion = false, stateNo, options }) {
    function parseStates(content) {
        if (!content)
            return ["", ""];
        if (!content.includes("|"))
            return [content, ""];
        return content.split("|");
    }
    function applyQuestionMark(value) {
        if (!value.includes('?'))
            return value;
        // todo: refine canBeQuestion and unit test it
        if (value === '?')
            return value;
        let [v, f] = value.split(':');
        let fmt = f ? `:${f}` : "";
        return canBeQuestion ?
            `?${fmt}` :
            value.replace('?', '');
    }
    let A = String.fromCharCode(65 + col);
    let name = `${A}${row + 1}`;
    let states = parseStates(content);
    states[0] = applyQuestionMark(states[0]);
    states[1] = states[1].replace('?', '');
    return {
        name,
        states,
        stateNo,
        options
    };
}
