import { DFmt } from "../../core/formatters/decimal-format";
import { takeIf } from "../../core/Helpers";
import { d2n, n2d, t2d } from "../../core/Helpers.convert";
import { assert, log, _l } from "../../core/Logger";
import { Config } from "../Sq.Config";
import { Formulas } from "../Sq.Formulas";
import { newVariable } from "../Sq.Variable";
function makeLambdas({ args, genLnNo, options, fmt }) {
    function getAb() {
        let [a, b] = args.rand(["a", "b"], genLnNo);
        let [sA, sB] = DFmt.formats([a, b], fmt);
        let op = b.lt(0) ? "-" : "+";
        let absB = DFmt.format(b.abs(), fmt);
        let opB = `${op} ${absB}`;
        return { a, b, sA, sB, opB };
    }
    function digitSum(x) {
        let sum = n2d(0);
        while (x.gt(0)) {
            let digit = x.mod(10);
            sum = sum.plus(digit);
            x = x.minus(digit).div(10);
        }
        return sum;
    }
    function digitRev(x) {
        let rev = "";
        while (x.gt(0)) {
            let digit = x.mod(10);
            rev += d2n(digit);
            x = x.minus(digit).div(10);
        }
        return t2d(rev);
    }
    let S = _ => `square`;
    let factories = {
        "x=>x*a+b": () => {
            let { a, b, sA, sB, opB } = getAb();
            return {
                apply: x => x.mul(a).plus(b),
                how: x => `${S(x)} xx ${sA} ${opB}`
            };
        },
        "x=>x/a+b": () => {
            let { a, b, sA, sB, opB } = getAb();
            return {
                apply: x => x.div(a).plus(b),
                how: x => `${S(x)} -: ${sA} ${opB}`
            };
        },
        "x=>(x+b)*a": () => {
            let { a, b, sA, sB, opB } = getAb();
            return {
                apply: x => x.plus(b).mul(a),
                how: x => `(${S(x)} ${opB}) xx ${sA}`
            };
        },
        "x=>(x+b)/a": () => {
            let { a, b, sA, sB, opB } = getAb();
            return {
                apply: x => x.plus(b).div(a),
                how: x => `(${S(x)} ${opB}) -: ${sA}`
            };
        },
        "x=>digitSum(x)": () => ({
            apply: x => digitSum(x),
            how: _ => `1+2+3 lArr 123`
        }),
        "x=>digitRev(x)": () => ({
            apply: x => digitRev(x),
            how: _ => `321 lArr 123`
        }),
    };
    function fromStr(name) {
        let factory = factories[name];
        assert(factory, `Can't find lambda definition for '${name}'`);
        return factory();
    }
    return {
        fromStr,
        get: (name) => fromStr(name)
    };
}
Formulas.registerFactory('magic-box', ({ args, genLnNo, options }) => {
    let { maxRetriesOnSameInput } = Config;
    let def = args.at("lambda");
    let _a0b0 = args.atLnNo(["a0", "b0", "c0", "d0", "e0"], genLnNo);
    let fmt = DFmt.parse(_a0b0);
    let [a0, b0, c0, d0, e0] = takeIf(maxRetriesOnSameInput, () => args.rand(["a0", "b0", "c0", "d0", "e0"], genLnNo), ar => ar.hasUniqueValues());
    let lambdas = makeLambdas({ args, genLnNo, options, fmt });
    let lambda = log.onerror(() => lambdas.fromStr(def), ex => _l(ex, { def, _a0b0, fmt }));
    let a1 = lambda.apply(a0);
    let b1 = lambda.apply(b0);
    let c1 = lambda.apply(c0);
    let d1 = lambda.apply(d0);
    let e1 = lambda.apply(e0);
    let $var = (value, name) => {
        let view = DFmt.format(value, fmt);
        return newVariable({ name, view, value, fmt });
    };
    let how = x => {
        let name = `how`;
        let sX = DFmt.format(x, fmt);
        let view = lambda.how(sX);
        return newVariable({ name, view, value: x, fmt });
    };
    function makeSequence() {
        return [
            $var(a0, "a0"),
            $var(b0, "b0"),
            $var(c0, "c0"),
            $var(d0, "d0"),
            $var(e0, "e0"),
            $var(a1, "a1"),
            $var(b1, "b1"),
            $var(c1, "c1"),
            $var(d1, "d1"),
            $var(e1, "e1"),
        ];
    }
    function makeVariables() {
        return [
            how(a0),
        ];
    }
    return {
        A: "",
        fmt,
        makeSequence,
        makeVariables
    };
});
