import JSON5 from "json5";
import { splitNoEmpty } from "./Helpers";
import { log, _l } from "./Logger";
class CAnki {
    hideLoader() {
        let loader = document.getElementById("loader");
        loader.style.display = "none";
    }
    getCard() {
        return ankiCard;
    }
    getConfig() {
        try {
            let config = ankiCard.config?.trim();
            if (!config || config.length === 0)
                config = ankiCard.defaultConfig?.trim();
            return JSON5.parse(config);
        }
        catch (err) {
            log.error("Can't get config from Anki card", err, x => _l(x));
            return {};
        }
    }
    *commands() {
        let content = ankiCard.content.replaceAll('\n', ' ');
        if (!content.includes(';'))
            return;
        let cmds = ankiCard.content.split(';')[0];
        for (let cmd of splitNoEmpty(cmds, ',')) {
            yield splitNoEmpty(cmd, ' ');
        }
    }
    tokens() {
        let content = ankiCard.content.replaceAll('\n', ' ');
        let tokens = content.includes(';') ?
            content.split(';')[1] :
            content;
        return splitNoEmpty(tokens, ',');
    }
}
export const Anki = new CAnki();
