<script context="module" lang="ts">

    import { Decimal } from "decimal.js"

    import { n2d } from "../core/Helpers.convert";
    import { rand, randFrom } from "../core/Helpers.rand";

    export type Float = number | Decimal;


    export function parseDecimal(str: string): Decimal {
        let parts = str.split('/');

        if (parts.length === 1)
            return new Decimal(str);

        let [n, d] = [
            new Decimal(parts[0]), 
            new Decimal(parts[1])
        ];

        let fr = n.div(d);

        return fr;
    }

    export function get(n: number[], nA: Decimal[]): Decimal {
        return nA.length > 0 ?
            randFrom(nA) :
            n2d(rand(n[0], n[1]));
    }

    export function percentsSimple(gameType: string, equ: string) {
        return gameType.includes("%") &&
            equ.includes("*");
    }

    export function percentsPlus(gameType: string, equ: string) {
        return gameType.includes("%") &&
            equ.includes("+");
    }

    export function percentsMinus(gameType: string, equ: string) {
        return gameType.includes("%") &&
            equ.includes("-");
    }

    export function makeProduct(min: Float, max: Float, nums: Float[], first?: Float, unique?: boolean): [Decimal, Decimal[]] {
        let nums2 = n2d([...nums]);
        let dividers: Decimal[] = [];
        let product: Decimal = n2d(first ?? 1);
        unique ??= true;

        let loopLimit = 20;

        do {
            let d = randFrom(nums2);

            if (product.mul(d).gt(max) && loopLimit-- > 0)
                continue;

            product = product.mul(d);

            dividers.push(d);

            if (unique)
                nums2.splice(nums2.indexOf(d), 1);

            if (nums2.length === 0) break;

        } while (product.lt(min) || dividers.length <= 1);

        return [product, dividers];
    }


</script>