<script lang="ts">
    import { onMount } from "svelte";

    import { Anki } from "../core/Anki";
    import { refreshMath } from "../core/MathJax.svelte";
    import { Mp3 } from "../core/Mp3.svelte";
    import Clock from './../core/Clock.svelte'
    import Grid from "../core/Grid.svelte"
    import Progress from "../core/Progress.svelte"
    import Timer from "../core/Timer.svelte"
    import Congrats from '../core/Congrats.svelte';
    import { run } from "../core/Helpers";

    import type { Config, Round } from "./Model.svelte";
    import { on, fire, newTimedCmd } from "./Model.svelte"
    import { newFrPairs } from "./Game.svelte";
    import Ans from "./Ans.svelte"
    import Question from "./Question.svelte";

    let timer: Timer;
    let answers: Ans[] = [null, null, null, null];
    let congrats: Congrats;

    let _recreate = [{}, {}, {}, {}];

    let _config = Anki.getConfig<Config>();
    let _secondsPerRound= 12;
    let _game = newFrPairs();
    let _round = _game.nextRound();
    let _state: "start" | "answered";
    let _progress: number[] = [1, 10];
    let _tcNewRound = newTimedCmd("--cmd-new-round", 1000);
    let _tcIdle = newTimedCmd("--cmd-idle", 10000, 3000);


    function init() {
        for (let cmd of Anki.commands()) {
            switch (cmd[0].toLocaleLowerCase()) {
                case "secondsPerRound": _secondsPerRound = parseInt(cmd[1]); break;
            }
        }
    }

    function newRound() {
        _recreate = _recreate.map(_ => new Object());
        _round = _game.nextRound();

        run(() => startRound());
    }

    function startRound() {
        _state = "start";

        _tcNewRound.cancel();
        _tcIdle.reset();

        answers.forEach(ans => {
            ans.setState("start");
        });

        _progress = _round.progress;

        run(() => refreshMath());
    }

    function queClick() {
        Mp3.playRandom();
    }

    function anyClick(): boolean {
        _tcIdle.reset();

        if (_state == "start")
            return false;

        if (_state == "answered") {
            newRound();
            return true;
        }
    }

    function gotAnswer(ans: string) {
        _tcIdle.reset();

        if (anyClick())
            return;

        _state = "answered";

        let answerAt = _round.answers.findIndex(x => x === ans);
        let rightAt = _round.rightAt;

        if (answerAt === rightAt) {
            _progress[0] += 1;
            let isLast = _game.isLast();
            Mp3.play(isLast ? "done" : "yes");
            _tcNewRound.reset();
            if (isLast)
                congrats.play();
        }
        else {
            answers[answerAt].setState("wrong");
            Mp3.play("no");
            _game.addRounds(2);
            _progress[0] += 1;
            _progress[1] += 2;
        }

        answers[rightAt].setState("right");
    }

    function idle() {
        Mp3.play("idle");
        _tcIdle.reset();
    }

    function timeIsUp() {
        _game.addRounds(1);
        _progress[1] += 1;
        timer.restart();
        Mp3.play("time-is-up");
    }

    init();

    on("--cmd-new-round", () => newRound());
    on("--cmd-idle", () => idle());
    on("--evt-time-is-up", () => timeIsUp());

    onMount(() => startRound());

    let layout = {
        areas: `
            "prg   .    clk "
            "que   que  que "
            "a0    .    a1  "
            ".     .    .   "
            "a2    .    a3  "
            ".     .    .   "
            "tm    tm   tm  "
        `,
        cols: `2fr 1fr 2fr`,
        rows: `50px 80px 70px 10px 70px 15px 5px`,
        bgColors: {
            prg: "bg-green-300",
            que: "bg-yellow-50 py-1",
            tm: "bg-blue-100"
        }
    };

</script>


<Grid {layout}
    on:click={() => anyClick()}
>
    <Progress ga="prg" values={_progress} />

    <Clock ga="clk" />

    {#key _recreate[0]}
    <Question ga="que"
        math={_round.fr}
        sz={36}
        on:click={() => queClick()}
    /> {/key}

    {#each _round.answers as ans, no (_recreate[no])}
        <Ans 
            ga={`a${no}`} 
            value={ans}
            bind:this={answers[no]} 
            on:click={() => gotAnswer(ans)}
        />
    {/each}

    <Timer ga="tm" seconds={_secondsPerRound * _config.rounds}
        bind:this={timer}
        onExpiration={() => fire("--evt-time-is-up")}
    />

    <Congrats bind:this={congrats} />
</Grid>