<script lang="ts">
    import type { Readable } from 'svelte/store';

    import { pulse } from './Pulse.svelte';
    import { bounceOnDrag } from "./OnDrag.svelte"

    export let ga: string;
    export let bgColor = "bg-green-300";

    export let store: {
        value: Readable<number>;
        max: Readable<number>;
    };

    let {value, max} = store;

    let [_left, _bar] = [$value, 0];

    $: {
        let left = $max - $value;
        if (left >= 0) {
            _left = left;
            _bar = 100 * ($value / $max);
        }
    }
</script>

<div 
    class="w-full bg-red-50 text-right"
    style="grid-area:{ga}" 
    use:pulse
    use:bounceOnDrag
>
    <div 
        class= " \
            text-xs text-center text-black \
            {bgColor} \
        "
        style="width:{_bar}%"
    >
        <span use:pulse>
            {_left}
        </span>
    </div>
</div>
