Set.prototype.diff = function (rhs) {
    let set = new Set(this);
    for (let x of rhs) {
        set.delete(x);
    }
    return set;
};
Set.prototype.includes = function (rhs) {
    for (let x of rhs) {
        if (!this.has(x))
            return false;
    }
    return true;
};
Set.prototype.include = function (rhs) {
    for (let x of rhs) {
        this.add(x);
    }
};
Set.prototype.str = function (sep = ',') {
    return `[${[...this].join(sep)}]`;
};
export {};
