import { DFmt } from "../core/formatters/decimal-format";
import { range } from "../core/Helpers";
import { rand, randFrom, shuffle } from "../core/Helpers.rand";
import { Config } from "./Sq.Config";
import { Formulas } from "./Sq.Formulas";
import { newVariable } from "./Sq.Variable";
import { assert } from "../core/Logger";
import { newCellAsRandomGenerator } from "../core/CellAsRandomGenerator";
import "./formulas/Sq.Formulas.a0+nd";
import "./formulas/Sq.Formulas.a0xd^n";
import "./formulas/Sq.Formulas.fib";
import "./formulas/Sq.Formulas.fib3";
import "./formulas/Sq.Formulas.op-table";
import "./formulas/Sq.Formulas.lambda";
import "./formulas/Sq.Formulas.+(a+)x(b+)";
import "./formulas/Sq.Formulas.magic-box";
import "./formulas/Sq.Formulas.math-op";
export function makeSqModel(definition, { numberOfLinesInGeneratorCell } = Config) {
    let { content, viewTemplate, options } = definition;
    let genLnNo = rand(numberOfLinesInGeneratorCell);
    let makeVar = ({ A, fmt, n, value, prefix, op }) => {
        let name = `${prefix}${A}${n}`;
        let sValue = DFmt.format(value, fmt);
        let sOp = op === "+" && value.lt(0) ? "" : op;
        let view = `${sOp}${sValue}`;
        return newVariable({ name, view, value, fmt });
    };
    function makeDifferences(seq, { A, fmt }) {
        let diffs = [];
        let add = (n, value, prefix, op) => {
            diffs.push(makeVar({ A, fmt, n, value, prefix, op }));
        };
        for (let j = 1; j < seq.length; j++) {
            let plus = seq[j].value.minus(seq[j - 1].value);
            let mul = seq[j].value.div(seq[j - 1].value);
            let div = seq[j - 1].value.div(seq[j].value);
            add(j, plus, "+", "+");
            add(j, mul, "*", "xx");
            add(j, div, "/", "-:");
        }
        return diffs;
    }
    function makeDifferences2(diffs, { A, fmt }) {
        let diffs2 = [];
        let add = (n, value, prefix, op) => {
            diffs2.push(makeVar({ A, fmt, n, value, prefix, op }));
        };
        let seq = diffs.filter(x => x.name.startsWith("+"));
        for (let j = 1; j < seq.length; j++) {
            let plus = seq[j].value.minus(seq[j - 1].value);
            let mul = seq[j].value.div(seq[j - 1].value);
            add(j + 1, plus, "++", "+");
            add(j + 1, mul, "+*", "xx");
        }
        return diffs2;
    }
    function makeFormulas(group) {
        // todo: instantiations should be unique (formula+a0+d0)
        let formulas = group.map(no => Formulas.makeFormula({
            args: content[no],
            options: definition.options,
            genLnNo,
            nCount: definition.nCount
        }));
        return formulas;
    }
    function makeVariables(formulas) {
        let varsOfContent = formulas.map(formula => {
            let seq = formula.makeSequence();
            let vars = formula.makeVariables();
            let diffs = makeDifferences(seq, formula);
            return {
                formula, seq, vars, diffs
            };
        });
        let seq = varsOfContent[0].seq.map((_, i) => varsOfContent.map(x => x.seq[i]))
            .flat();
        let fmt = DFmt.parse(varsOfContent[0].formula.fmt, ...varsOfContent.map(x => x.formula.fmt));
        let diffs = makeDifferences(seq, { A: "", fmt });
        let diffs2 = makeDifferences2(diffs, { A: "", fmt });
        let vars = diffs
            .concat(diffs2, ...varsOfContent.map(x => [x.seq, x.vars, x.diffs]).flat())
            .sort((a, b) => b.name.length - a.name.length);
        return vars;
    }
    function substitute({ template, vars, variate = false }) {
        let view = template;
        vars.forEach($var => view = $var.toUniqueName(view));
        vars.forEach($var => view = $var.toView({ str: view, variate }));
        return view;
    }
    function makeAnswers(vars) {
        assert(definition.answers.count > 0, "No answers in definition");
        let answers = definition.answers.all()
            .filter(x => x.trim().length > 0) // no empty answers
            .map((def, indx) => ({
            isRight: indx == 0,
            value: substitute({
                template: def,
                vars,
                variate: true
            }),
        }))
            .removeDuplicates((lhs, rhs) => lhs.value === rhs.value);
        let [first, ...others] = answers;
        let threeOthers = shuffle(others).take(3);
        return [first, ...threeOthers];
    }
    function makeView(vars, answers) {
        let view = viewTemplate
            .map(ln => ln
            .map(cell => substitute({
            template: cell,
            vars
        })));
        return view;
    }
    function filterActiveFormulas() {
        // todo: unit test
        let active = range(content.numOfLines);
        let formulas = options.get("formulas");
        if (formulas) {
            active = newCellAsRandomGenerator(formulas)
                .all()
                .map(x => x - 1); // from line numbers to indexes
            console.log(`Filtering formulas to `, active);
        }
        return range(content.numOfLines)
            .filter(row => active.includes(row));
    }
    function makeFormulaGroup(active) {
        // todo: unit test
        let map = new Map();
        for (let row of active) {
            let formula = content.at("formula", row);
            let id = formula.splitTrim('|')[1] ?? "default";
            let group = map.get(id) ?? [];
            group.push(row);
            map.set(id, group);
        }
        let id = randFrom([...map.keys()]);
        return map.get(id);
    }
    let problemId = definition.problemId;
    let activeFormulas = filterActiveFormulas();
    let group = makeFormulaGroup(activeFormulas);
    assert(group.length > 0, "Failed to group formulas in definition");
    let formulas = makeFormulas(group);
    let vars = makeVariables(formulas);
    let answers = makeAnswers(vars);
    let view = makeView(vars, answers);
    let settings = definition.settings;
    return {
        problemId,
        content,
        settings,
        view,
        answers,
        options
    };
}
