<script context="module" lang="ts">

    import {fireAny} from "./IoBus.svelte"
    import { rand } from "./Helpers.rand";

    export interface ITimedCmd {
        reset();
        cancel();
    }

    export class CTimedCmd implements ITimedCmd {
        _cmd: string;
        _timeout: number;
        _jitter: number;
        _timeoutId: any;

        constructor(cmd: string, timeout: number, jitter?: number) {
            this._cmd = cmd;
            this._timeout = timeout;
            this._jitter = jitter ?? 0;
        }

        set() {
            let timeout = 
                rand(this._timeout - this._jitter, this._timeout + this._jitter);

            this._timeoutId = setTimeout(
                () => fireAny(this._cmd, {}), timeout
            );
        }

        cancel() {
            clearTimeout(this._timeoutId);
        }

        reset() {
            this.cancel();
            this.set();
        }
    }

</script>
