import { DFmt } from "../../core/formatters/decimal-format";
import { Formulas } from "../Sq.Formulas";
import { newVariable } from "../Sq.Variable";
Formulas.registerFactory(['fib', 'fib-', 'fib*'], ({ args, genLnNo, nCount }) => {
    let formula = args["formula"];
    let op = formula.includes('-') ? "-" :
        formula.includes('*') ? "*" :
            "+";
    let _subst = args["subst"];
    let _a0 = args.atLnNo("a0", genLnNo);
    let A = _subst.split(':')[0];
    let a0 = args.rand("a0", genLnNo);
    let a1 = args.rand("a1", genLnNo);
    let fmt = DFmt.parse(_subst, _a0);
    let $var = (n, a) => {
        let name = `${A}${n}`;
        let view = DFmt.format(a, fmt);
        return newVariable({ name, view, value: a, fmt });
    };
    function makeSequence() {
        let seq = [
            $var(0, a0),
            $var(1, a1),
        ];
        for (let n = 2; n <= nCount; n++) {
            let a = op === "-" ? a0.minus(a1) :
                op === "*" ? a0.mul(a1) :
                    a0.plus(a1);
            seq.push($var(n, a));
            [a0, a1] = [a1, a];
        }
        return seq;
    }
    function makeVariables() {
        return [];
    }
    return {
        A,
        fmt,
        makeSequence,
        makeVariables
    };
});
