<script lang="ts">

    import type { Decimal } from "decimal.js"

    import { pulse } from '../core/Pulse.svelte';
    import { getBgColor } from '../core/Grid.svelte';
    import { toAscii, fractionToMath } from "../core/AsciiMathUtils";
    import { bounceOnDrag } from "../core/OnDrag.svelte"

    export let ga: string;
    export let num: Decimal;
    export let asAsciiMath: boolean;

    export let sz: number = 30;

    function n2a(n: Decimal): [string, number] {
        let str = n.toNumber().toLocaleString('en');
        let [isFraction, math, len] = fractionToMath(n);

        if (!isFraction || !asAsciiMath)
            return [str, str.length];

        if (isFraction)
            len += 2;

        return [toAscii(math), len];
    }

    $: [_text, _len] = n2a(num);
    $: _sz = sz - 1.2 * _len;

    let _bgColor = getBgColor(ga);

</script>


<button
    class="{_bgColor}"
    style="grid-area:{ga};font-size:{_sz}px" 
    use:pulse
    use:bounceOnDrag
>
    {_text}
</button>
