<script lang="ts">
    import { tick } from "svelte";
    import { Decimal } from "decimal.js"

    import { refreshMath } from "../core/MathJax.svelte";
    import { Anki } from "../core/Anki"
    import { Mp3 } from "../core/Mp3.svelte";
    import Grid from './../core/Grid.svelte'
    import Clock from './../core/Clock.svelte'
    import Congrats from '../core/Congrats.svelte';

    import { on, fire } from "./Model.svelte"
    import { newTimedCmd } from "./Model.svelte";
    import Count from "./Count.svelte"
    import Num from "./Num.svelte"
    import Ans from "./Ans.svelte"
    import Op from "./Op.svelte"

    import type { Config, Round } from "./Model.svelte"
    import { pm, rand, randFrom, shuffle, trueFalse } from "../core/Helpers.rand";
    import { n2d } from "../core/Helpers.convert";

    let congrats: Congrats;

    let _game: Round;

    let _current = 0;
    let _rounds: Round[] = [];

    $: _roundsLeft = _rounds.length - _current;

    let _withFractions: boolean = false;
    let _config = Anki.getConfig<Config>();

    let _tcNewRound = newTimedCmd("--cmd-next-round", 1000);
    let _tcIdle = newTimedCmd("--cmd-idle", 7000, 3000);

    let _a: number[] = [1, 9];
    let _b: number[] = [2, 9];
    let _aA: number[] = [];
    let _bA: number[] = [];
    let _lastDigit: string = null;

    let answers: Ans[] = [null, null, null];
    let recreate = answers.map(_ => new Object());

    interface AbPair {
        a: Decimal;
        b: Decimal;
        wrong: Decimal;
        fa: Decimal;
        fb: Decimal;
    }

    let D = (n: number) => new Decimal(n);

    function getAb(): Decimal[] {
        let a = D(_aA.length > 0 ?
            randFrom(_aA) :
            rand(_a[0], _a[1])
        );

        let b = D(_bA.length > 0 ?
            randFrom(_bA) :
            rand(_b[0], _b[1])
        );

        return [a, b];
    }

    function getAbPairWithDots(line: number[]): AbPair {

        let [za, zb] = shuffle(line);

        let fa = za < 0 ?
            Decimal.pow(10, rand(za, -1)) :
            Decimal.pow(10, rand(1, za));

        let fb = zb < 0 ?
            Decimal.pow(10, rand(zb, -1)) :
            Decimal.pow(10, rand(1, zb));

        let [a, b] = getAb();

        let wrong = b.plus(pm(1));

        a = a.mul(fa);
        b = b.mul(fb);
        wrong = wrong.mul(fb);

        return {a, b, wrong, fa, fb};
    }

    function getAbPair(): AbPair {
        let line = Anki.tokens().map(x => parseInt(x));

        if (line[0] < 0 || line[1] < 0)
            return getAbPairWithDots(line);

        let [a, b] = getAb();

        let wrong = b.plus(pm(1));
        let fa = D(1);
        let fb = D(1);

        let noOfZeros = rand(line[0], line[1]);

        for (let no = noOfZeros; no-- > 0; ) {
            if (a.equals(1)) {                  // ensure first factor is not one
                a = a.mul(10);
                fa = fa.mul(10);
                continue;
            }

            switch (trueFalse()) {
                case true:
                    a = a.mul(10);
                    fa = fa.mul(10);
                    break;
                case false:
                    b = b.mul(10);
                    fb = fb.mul(10);
                    wrong = wrong.mul(10);
                    break;
            }
        }

        return {a, b, wrong, fa, fb};
    }

    function addMulRound() {
        let op = "x";

        let {a, b, wrong} = getAbPair();

        let lhs = a;
        let rhs = b;

        let a0 = lhs.mul(rhs);
        let a1 = lhs.mul(wrong);

        let a2: Decimal, a3: Decimal;

        switch (trueFalse()) {
            case true:
                a2 = a0.mul(10);
                a3 = a1.mul(10);
                break;
            case false:
                a2 = a0.div(10);
                a3 = a1.div(10);
                break;
        }

        let nums = shuffle([lhs, rhs]);
        let answers = shuffle([a0, a1, a2, a3]);
        let asAsciiMath = answers.map(_ => trueFalse());

        let rightAt = answers.findIndex(x => x == a0);

        _rounds.push({
            nums, op, answers, asAsciiMath, rightAt
        });
    }

    function addDivRound() {
        let op = "/";

        let {a, b, wrong} = getAbPair();

        let lhs = a.mul(b);
        let rhs = a;

        let a0 = b;
        let a1 = wrong;

        let a2: Decimal, a3: Decimal;

        switch (trueFalse()) {
            case true:
                a2 = a0.mul(10);
                a3 = a1.mul(10);
                break;
            case false:
                a2 = a0.div(10);
                a3 = a1.div(10);
                break;
        }

        let nums = [lhs, rhs];
        let answers = shuffle([a0, a1, a2, a3]);
        let asAsciiMath = answers.map(_ => trueFalse());

        let rightAt = answers.findIndex(x => x === a0);

        _rounds.push({
            nums, op, answers, asAsciiMath, rightAt
        });
    }

    let downTo10 = (d: Decimal) => d.minus(d.mod(10));
    let upTo10 = (d: Decimal) => d.plus(D(10).minus(d.mod(10)));

    function addLastDigitMulRound() {
        let op = "x";

        let [a, b] = getAb();

        while (a.mod(10).eq(0) || b.mod(10).eq(0))
            [a, b] = getAb();

        let a0 = a.mul(b);
        let lastDigit = a0.mod(10);

        let low = downTo10(a).mul(downTo10(b));
        let high = upTo10(a).mul(upTo10(b));
        let f = n2d(rand(1, 10)).mul(10)

        let a1 = trueFalse() ?
            low.minus(f).plus(lastDigit) :
            high.plus(f).plus(lastDigit);

        let d = pm(rand(1, 9));
        let a2 = a0.plus(d);
        let a3 = a1.plus(d).plus(pm(randFrom([10, 20, 30])));

        let nums = [a, b];
        let answers = shuffle([a0, a1, a2, a3]);
        let rightAt = answers.findIndex(x => x === a0);

        let asAsciiMath = answers.map(_ => false);

        _rounds.push({
            nums, op, answers, asAsciiMath, rightAt
        });
    }

    function addLastDigitDivRound() {
        let op = ":";
        let [a, b] = getAb();

        while (a.mod(10).eq(0) || b.mod(10).eq(0))
            [a, b] = getAb();

        let r0 = a.mul(b);

        let a0 = a;
        let a1 = a0.plus(pm(10));

        let a2 = a0.plus(pm(rand(1, 9)));

        // ambiguity of division; f.e. 1634 : 38 => 43, 38, 53, 48
        while (a2.mul(b).mod(10).eq(r0.mod(10)))
            a2 = a0.plus(pm(rand(1, 9)));

        let a3 = a2.plus(pm(10));

        let nums = [r0, b];
        let answers = shuffle([a0, a1, a2, a3]);
        let rightAt = answers.findIndex(x => x === a0);

        let asAsciiMath = answers.map(_ => false);

        _rounds.push({
            nums, op, answers, asAsciiMath, rightAt
        });
    }

    function addNextRounds() {
        let mulChars = "*x";
        let divChars = "/\\:";

        if (_lastDigit)
        {

            if (_lastDigit.length > 1) {
                switch (trueFalse()) {
                    case true:
                        addLastDigitMulRound();
                        addLastDigitDivRound();
                        break;
                    case false:
                        addLastDigitDivRound();
                        addLastDigitMulRound();
                        break;
                }
                return;
            }

            if (mulChars.includes(_lastDigit)) {
                addLastDigitMulRound();
                addLastDigitMulRound();
                return;
            }

            if (divChars.includes(_lastDigit)) {
                addLastDigitDivRound();
                addLastDigitDivRound();
                return;
            }
        }

        addMulRound();
        addDivRound();
    }

    async function nextRound() {
        _tcNewRound.cancel();
        _tcIdle.reset();

        if (++_current === _rounds.length)
            addNextRounds();

        _game = _rounds[_current];

        answers.forEach(ans => ans.setBgColor("bg-gray-100"));
        recreate = answers.map(_ => new Object());

        await tick();
        refreshMath();
    }

    function init() {
        _tcIdle.reset();

        let cmds = Anki.commands();
        for (let cmd of cmds) {
            switch (cmd[0]) {
                case "dots": break;
                case "with-fractions": 
                    _withFractions = true;
                    break;
                case "last-digit": _lastDigit = cmd[1]; break;
                case "a": _a = cmd.slice(1).map(x => parseInt(x)); break;
                case "b": _b = cmd.slice(1).map(x => parseInt(x)); break;
                case "a[]": _aA = cmd.slice(1).map(x => parseInt(x)); break;
                case "b[]": _bA = cmd.slice(1).map(x => parseInt(x)); break;
            }
        }

        let noOfRounds = _config.rounds ?? 4;

        for (let cnt = noOfRounds; cnt-- > 0; )
            addNextRounds();

        _game = _rounds[_current];

        refreshMath();
    }

    function select(no: number) {
        _tcIdle.reset();

        if (_game.clickedAt !== undefined) {
            fire("--cmd-next-round");
            return;
        }

        _game.clickedAt = no;

        if (no === _game.rightAt) {
            if (_current == _rounds.length - 1) {
                Mp3.play("done");
                congrats.play();
            } else {
                Mp3.play("yes");
            }
            _tcNewRound.reset();
        } else {
            Mp3.play("no");
            answers[no].setBgColor("bg-red-200");
            addNextRounds();
            _roundsLeft = _rounds.length - _current;
        }

        answers[_game.rightAt].setBgColor("bg-green-200");
    }

    function onClick() {
        if (_game.clickedAt !== undefined)
            fire("--cmd-next-round");
    }

    function idle() {
        _tcIdle.reset();
        Mp3.play("idle");
    }

    on("--cmd-next-round", () => nextRound());
    on("--evt-click", () => onClick());
    on("--cmd-idle", () => idle());
    
    init();

    let layout = {
        areas: `
            "cnt   .    clk "
            "lhs   op   rhs "
            "a0    .    a1  "
            "a2    .    a3  "
        `,
        cols: `2fr 1fr 2fr`,
        rows: `60px 80px 80px 80px`,
        bgColors: {
            cnt: "bg-purple-300",
            lhs: "bg-green-100",
            op:  "bg-yellow-200",
            rhs: "bg-green-100"
        }
    };

</script>

<Grid {layout}
    on:click={() => fire("--evt-click")}
>
    <Count ga="cnt" count={_roundsLeft}/>
    <Clock ga="clk" />

    {#key recreate[0]}
    <Num ga="lhs" 
        num={_game.nums[0]} 
        asAsciiMath={_withFractions && trueFalse()}
    /> {/key}

    <Op  ga="op"  op={_game.op} />

    {#key recreate[0]}
    <Num ga="rhs" 
        num={_game.nums[1]} 
        asAsciiMath={_withFractions && trueFalse()}
    /> {/key}

    {#each _game.answers as ans, i (recreate[i]) }
        <Ans 
            ga={`a${i}`} 
            num={ans}
            asAsciiMath={_withFractions && _game.asAsciiMath[i]}
            bind:this={answers[i]}
            on:click={() => select(i)}
        />
    {/each}

    <Congrats bind:this={congrats} />
</Grid>
