<script lang="ts">

    import { onMount } from "svelte";
    
    import { range } from "./Helpers"
    import { rand, randFrom } from "./Helpers.rand"

    import anime from 'animejs/lib/anime.es'

    let config = {
        size: 18,
        pos: [100, 120],
        xSeed: [20, 100],
        ySeed: [20, 120],
        duration: 1500
    }

    interface Star {
        box: HTMLElement
        color: string
    }

    let stars: Star[];
    let hidden: boolean = true;
    
    function reset() {
        anime({
            targets: stars.map(star => star.box),
            translateX: 0,
            translateY: 0,
            rotate: 0,
            opacity: 1,
            duration: 0
        });
    }

    export function play() {
        let delay = () => rand(150, 300);
        let translateX = () => randFrom([1, -1]) * (config.xSeed[0] + rand(0, config.xSeed[1]));
        let translateY = () => randFrom([1, -1]) * (config.ySeed[0] + rand(0, config.ySeed[1]));
        let rotate = () => rand(200, 360);
        let scale = () => 1 + rand(1, 10) / 10;

        reset();
        hidden = false;

        anime({
            targets: stars.map(star => star.box),
            translateX,
            translateY,
            rotate,
            scale,
            delay,
            opacity: {
                value: 0,
                easing: 'linear',
                delay: config.duration - 200,
                duration: 500
            },
            easing: 'easeOutElastic',
            duration: config.duration,
            complete: () => hidden = true
        })
    }    

    export let noOfStars: number = 150;

    stars = range(0, noOfStars).map(() => { return {
        box: undefined,
        color: randFrom(['yellow', 'orange', 'red'])
    }});

    onMount(reset)

</script>


<div class:hidden>
    {#each stars as star}
        <div class="box"
            bind:this={star.box}
            style="
                --size: {config.size}px;
                --left: {config.pos[0]}px;
                --top: {config.pos[1]}px;
            "
        >
            <svg xmlns="http://www.w3.org/2000/svg" 
                viewBox="0 0 512 512">
                <polygon 
                    fill={star.color}
                    stroke={star.color} 
                    stroke-width="37.6152" 
                    stroke-linecap="round" 
                    stroke-linejoin="round" 
                    stroke-miterlimit="10" 
                    points="259.216,29.942 330.27,173.919 489.16,197.007 374.185,309.08 
                            401.33,467.31 259.216,392.612 117.104,467.31 144.25,309.08 
                            29.274,197.007 188.165,173.919"
                />
            </svg>
        </div>
    {/each}
</div>


<style>
    .hidden {
        display: none;
    }
    .box {
        position: absolute;
        width: var(--size);
        height: var(--size);
        left: var(--left);
        top: var(--top);
        z-index: 1;
    }
</style>