<script context="module" lang="ts">

    import { fire } from "./Model.svelte";
    import type {Game} from "./Model.svelte"

    interface IGcdLcm {
        readonly game: Game;
        newRound();
        go();
        done();
        idle();
    }

    let _game: Game;

    class CGcdLcm implements IGcdLcm {

        get game() {
            return _game;
        }

        newRound() {
            _game = {
                roundsLeft: 2,
                nums: [4, 5],
                divisors: [
                    { factor: 2, mxPow: 3 },
                    { factor: 3, mxPow: 3 },
                    { factor: 5, mxPow: 2 },
                    { factor: 7, mxPow: 1 },
                ]
            }

            fire("--cmd-set-models", _game);
        }

        go() {
            
        }

        done() {}

        idle() {}
    }

    export const GcdLcm: IGcdLcm = new CGcdLcm();

</script>

