import { toAscii } from "../AsciiMathUtils";
import { d2t, d2t5, n2d, t2d } from "../Helpers.convert";
import { randFrom } from "../Helpers.rand";
import { isSimpleFraction } from "./g2l-formatter";
export function percents(value) {
    if (value.includes("/"))
        return [toAscii(value)];
    let d = n2d(parseFloat(value));
    if (d.isNaN())
        return [];
    switch (randFrom(["percents", "fraction", "dots"])) {
        case "percents": return [`${d.mul(100)}%`];
        case "fraction": return [toAscii(d2t(d))];
        case "dots": return [value];
    }
}
function percents2(value, fmt, simpleFractionFactors = []) {
    let _d = t2d(value);
    if (_d.isNaN())
        return [];
    let _variations = [];
    let _isFixed = _d.minus(_d.toDP(3)).eq(0);
    let _isSimpleFraction = isSimpleFraction(_d);
    let percents = `${_d.mul(100)}%`;
    let dots = _d.toString();
    let fraction = toAscii(d2t(_d));
    if (_isFixed) {
        _variations.push(dots);
        _variations.push(percents);
    }
    if (_isSimpleFraction) {
        _variations.push(fraction);
        simpleFractionFactors.forEach(f => _variations.push(toAscii(d2t5(_d, f))));
    }
    if (_variations.length === 0) {
        _variations.push(value.includes('/') ? toAscii(value) : value);
    }
    return _variations;
}
export const p2 = (value, fmt) => percents2(value, fmt);
export const p3 = (value, fmt) => percents2(value, fmt, [2, 3, 4, 5]);
