import { ankiDroid } from "../core/AnkiDroid";
export function newHelpMe(store) {
    let _store = store;
    let _handle;
    function start() {
        let isClicked = ankiDroid.isCurrentCardMarked();
        store.clicked.set(isClicked);
        clearTimeout(_handle);
        _handle = setTimeout(() => _store.here.set(true), store.timeout);
    }
    return {
        store,
        start: () => start(),
        restart: () => {
            _store.here.set(false);
            start();
        },
        apply: () => {
            store.clicked.set(true);
            ankiDroid.markCurrentCard();
        }
    };
}
