<script context="module" lang="ts">

    import { delay, safe, run } from "./Helpers";

    declare var MathJax;

    let _initialized: boolean = false;
    let _refreshIsRequested: boolean = false;

    // refreshes are delayed on initialization

    function refresh() {
        if (!_refreshIsRequested) return;

        _refreshIsRequested = false;

        safe(() => MathJax.typeset());
    }

    export function refreshMath() {
        _refreshIsRequested = true;

        if (!_initialized) return;

        run(refresh);
    }

    export function initMath() {
        run(async () => {
            await delay(2000);
            _initialized = true;
            refresh();
        });
    }
</script>
