String.prototype.splitTrim = function (sep) {
    return this
        .split(sep)
        .map(x => x.trim());
};
String.prototype.splitTrimNoEmpties = function (sep) {
    return this
        .split(sep)
        .map(x => x.trim())
        .filter(x => x.length > 0);
};
String.prototype.isAsciiMath = function () {
    let str = this;
    return str.startsWith('`') && str.endsWith('`');
};
String.prototype.toAsciiMath = function () {
    let noAsciiMath = this.replaceAll('`', '');
    return `\`${noAsciiMath}\``;
};
export {};
