<script lang="ts">

    import { tick } from "svelte";
    import { onMount } from "svelte";

    import type { Decimal } from "decimal.js"

    import { refreshMath } from "../core/MathJax.svelte";
    import { Mp3 } from "../core/Mp3.svelte";
    import AsciiMath from "../core/AsciiMath.svelte"
    import Clock from './../core/Clock.svelte'
    import Grid from "../core/Grid.svelte"
    import Progress from "../core/Progress.svelte"
    import Timer from "../core/Timer.svelte"
    import Congrats from '../core/Congrats.svelte';
    import { run } from "../core/Helpers";

    import { newEquation } from "./Game.svelte"
    import { on, fire, newTimedCmd } from "./Model.svelte";
    import type { Round } from "./Model.svelte"
    import Ans from "./Ans.svelte"

    let timer: Timer;
    let congrats: Congrats;

    let _game = newEquation();
    let _round = _game.nextRound();
    let _answers: Ans[] = [null, null, null, null];
    let _state: "start" | "clicked" | "answered";
    let _progress: number[] = [1, 10];
    let _tcNewRound = newTimedCmd("--cmd-new-round", 1000);
    let _tcIdle = newTimedCmd("--cmd-idle", 10000, 3000);

    let _recreate = _answers.map(_ => new Object());

    function equClick() {
        Mp3.playRandom();
        switch (_round.gameType) {
            case "dividers-2":
                newRound();
                break;
        }
    }

    function anyClick(): boolean {
        _tcIdle.reset();

        if (_state == "start") {
            _state = "clicked";
            _answers.forEach(ans => ans.setState("clicked"));

            run(() => refreshMath());

            return true;
        }

        if (_state === "clicked")
            return false;

        if (_state == "answered") {
            newRound();
            return true;
        }

        return true;
    }

    function gotAnswer(value: Decimal) {
        _tcIdle.reset();

        if (anyClick())
            return;

        if (_state === "clicked") {
            _state = "answered";

            let answerAt = _round.answers.findIndex(x => x.eq(value));
            let rightAt = _round.answers.findIndex(x => x.eq(_round.rightAns));

            if (answerAt === rightAt) {
                _progress[0] += 1;
                let isLast = _game.isLast(_round);
                Mp3.play(isLast ? "done" : "yes");
                _tcNewRound.reset();
                if (isLast)
                    congrats.play();
            } 
            else {
                _answers[answerAt].setState("wrong");
                Mp3.play("no");
                _game.addRounds(2);
                _progress[0] += 1;
                _progress[1] += 2;
            }

            _answers[rightAt].setState("right");
        }
    }

    async function newRound() {
        _recreate = _answers.map(_ => new Object());
        _round = _game.nextRound();
        await tick()
        startRound();
    }

    function idle() {
        Mp3.play("idle");
        _tcIdle.reset();
    }

    function startRound() {
        _state = "start";

        _tcIdle.reset();
        _tcNewRound.cancel();

        _answers.forEach(ans => {
            ans.setState("start");
        });

        _progress = _round.progress;

        switch (_round.gameType) {
            case "dividers":
            case "dividers-2":
            case "dividers+-":
                anyClick();
                break;
        }
    }

    function getScale(round: Round): number {
        if (round.mathScale)
            return round.mathScale;

        switch (round.gameType) {
            case "%-simple": 
            case "%-plus": 
            case "%-minus": 
            case "%-all":
                return 1.2;
            case "dividers":
                return 0.3;
            case "dividers-2":
                return 0.4;
            case "dividers+-":
                return 0.35;
        }

        return undefined;
    }

    function getAnsUnits(round: Round): string {
        switch (round.gameType) {
            case "%-a-of-c": return "%";
        }

        return "";
    }

    function timeIsUp() {
        _game.addRounds(1);
        _progress[1] += 1;
        timer.restart();
        Mp3.play("time-is-up");
    }

    onMount(() => startRound());

    on("--cmd-new-round", () => newRound());
    on("--cmd-idle", () => idle());
    on("--evt-time-is-up", () => timeIsUp());

    let layout = {
        areas: `
            "prg   .    clk "
            "equ   equ  equ "
            "a0    .    a1  "
            "a2    .    a3  "
            "tm    tm   tm  "
        `,
        cols: `2fr 1fr 2fr`,
        rows: `50px 80px 80px 80px 5px`,
        bgColors: {
            prg: "bg-green-300",
            equ: "bg-yellow-50",
            tm: "bg-blue-100"
        }
    };

</script>

<Grid {layout}
    on:click={() => anyClick()}
>
    <Progress ga="prg" values={_progress} />

    <Clock ga="clk" />

    <AsciiMath 
        ga="equ"
        sz={40}
        exp={_round.equ}
        scale={getScale(_round)}
        on:click={() => equClick()}
    />

    {#each _round.answers as ans, no (_recreate[no])}
        <Ans
            gameType={_round.gameType}
            ga={`a${no}`} 
            value={ans}
            units={getAnsUnits(_round)}
            bind:this={_answers[no]} 
            on:click={() => gotAnswer(ans)}
        />
    {/each}

    <Timer ga="tm" seconds={_round.seconds}
        bind:this={timer}
        onExpiration={() => fire("--evt-time-is-up")}
    />
    
    <Congrats bind:this={congrats} />
</Grid>
