import { DFmt } from "../../core/formatters/decimal-format";
import { d2n, n2d } from "../../core/Helpers.convert";
import "../../core/Logger";
import { Formulas } from "../Sq.Formulas";
import { newVariable } from "../Sq.Variable";
function opToAscii(op) {
    switch (op) {
        case "⊕": return 'o+';
        case "⊙": return 'o.';
        case "⊗": return 'ox';
        case "&": return '\\ "&"\\ ';
        case "@": return '\\ "@"\\ ';
        case "#": return '\\ "#"\\ ';
        case "⋆": return '***';
        case "⋂": return 'nnn';
        case "△": return '/_\\';
        default: return op;
    }
}
function apply({ fn, a, b, x, y }) {
    return fn(x, y);
}
function calc({ def, a, b, x, y }) {
    let fn = eval(def);
    let r = apply({ fn, a: d2n(a), b: d2n(b), x: d2n(x), y: d2n(y) });
    return n2d(r);
}
Formulas.registerFactory('math-op', ({ args, genLnNo, options }) => {
    let _abx = args.atLnNo(["a", "b", "x", "y"], genLnNo);
    let fmt = DFmt.parse(_abx);
    let [a, b, x, y] = args.rand(["a", "b", "x", "y"], genLnNo);
    let [def, X, Y] = args.atLnNo(["def", "X", "Y"], genLnNo);
    let op = args.rand_str("op", genLnNo);
    let lambda = args.lines("lambda");
    let r0 = calc({ def: lambda[0], a, b, x, y });
    let r1 = calc({ def: lambda[1], a, b, x, y });
    let r2 = calc({ def: lambda[2], a, b, x, y });
    let r3 = calc({ def: lambda[3], a, b, x, y });
    let [sA, sB] = DFmt.formats([a, b], fmt);
    def = def
        .replaceAll("a", sA)
        .replaceAll("b", sB)
        .replaceAll("X", X)
        .replaceAll("Y", Y);
    let sOp = opToAscii(op);
    let $var = (value, name) => {
        let view = DFmt.format(value, fmt);
        return newVariable({ name, view, value, fmt });
    };
    let str = (view, name) => {
        let value = n2d(0);
        return newVariable({ name, view, value, fmt });
    };
    function makeSequence() {
        return [
            $var(x, "x"),
            $var(y, "y"),
        ];
    }
    function makeVariables() {
        return [
            str(X, "X"),
            str(Y, "Y"),
            str(sA, "a"),
            str(sB, "b"),
            str(sOp, "op"),
            str(def, "def"),
            $var(r0, "r0"),
            $var(r1, "r1"),
            $var(r2, "r2"),
            $var(r3, "r3"),
        ];
    }
    return {
        A: "",
        fmt,
        makeSequence,
        makeVariables
    };
});
