import "../core/extensions/all.extensions";
import { newCellAsRandomGenerator } from "./CellAsRandomGenerator";
import { WithIndex } from "./Extensions";
import { fixCrInCells } from "./Helpers";
import { t2d, n2d, isArrayIndex } from "./Helpers.convert";
import { assert, throwError, _l } from "./Logger";
import { randFrom } from "./Helpers.rand";
// todo: check for same names
export function newArgsGrid(_grid) {
    if (!_grid || _grid.length <= 1 || _grid[0].length === 0)
        throwError(`Undefined or empty grid`, x => _l(x));
    let [_names, ..._args] = _grid;
    let _lines = _args.map(row => row.join(','));
    let [_dy, _dx] = [_args.length, _args[0].length];
    function get(key, row, fail, throughIndex = false) {
        let error = msg => fail ?
            throwError(`${msg} in ${row} row (${_lines[row]})`, x => _l(x)) : undefined;
        if (row < 0 || row >= _dy)
            return error(`Row ${row} is out of grid (${_dy} lines)`);
        if (isArrayIndex(key)) {
            if (throughIndex) {
                if (key < 0 || key >= _dy)
                    return error(`Row ${key} is out of grid (${_dy} lines)`);
                return key === 0 ? this :
                    newArgsGrid([_names, ..._args.slice(key)]);
            }
            if (key < 0 || key >= _dx || key >= _args[row].length)
                return error(`No ${throughIndex ? "line" : "index"} '${key}'`);
            return _args[row][key];
        }
        let indx = _names.indexOf(key);
        if (indx === -1)
            return error(`No key '${key}'`);
        if (indx >= _args[row].length)
            return error(`No value for '${key}' at ${indx} (row.length=${_args[row].length})`);
        return _args[row][indx];
    }
    function v2i(name, row = 0, fail = false) {
        let v = get(name, row, fail);
        let error = msg => throwError(`v2i('${name}', ${row}): ${msg} while converting v='${v}'`);
        if (v === undefined) {
            if (!fail)
                return undefined;
            error("No key");
        }
        if (v.length === 0) {
            if (!fail)
                return undefined;
            error("Empty value");
        }
        let i = parseInt(v);
        let d = n2d(i);
        try {
            d = t2d(v);
        }
        catch {
            if (!fail)
                return i;
            error("NaN (string)");
        }
        if (!d.eq(i)) {
            if (!fail)
                return i;
            error("NaN (not integer)");
        }
        if (Number.isNaN(Number(v))) {
            if (!fail)
                return i;
            error("NaN (string)");
        }
        return i;
    }
    function v2d(name, row = 0, fail = false) {
        let v = get(name, row, fail);
        let error = msg => throwError(`v2d('${name}', ${row}): ${msg} while converting v='${v}'`);
        if (v === undefined) {
            if (!fail)
                return undefined;
            error("No key");
        }
        if (v.length === 0) {
            if (!fail)
                return undefined;
            error("Empty value");
        }
        let d = t2d(v);
        if (d.eq(n2d(Infinity))) {
            if (!fail)
                return d;
            error("Infinity in value");
        }
        return d;
    }
    function _rand_str(name, lnNo, row) {
        let cell = get(name, row, true);
        let lines = cell.splitTrim(';');
        if (lnNo >= 0) { // if specified take only one row
            lines = [lines[lnNo % lines.length]];
        }
        let values = lines
            .map(ln => ln.split(','))
            .flat();
        return randFrom(values);
    }
    function _rand(name, lnNo, row) {
        let cell = get(name, row, true);
        let gen = newCellAsRandomGenerator(cell, lnNo);
        return t2d(gen.one());
    }
    function rand_str(names, lnNo = -1, row = 0) {
        if (typeof names === 'string' || typeof names === 'number')
            return _rand_str(names, lnNo, row);
        assert(names.length > 0, "Should be one name at least");
        return names.map(x => _rand_str(x, lnNo, row));
    }
    function rand(names, lnNo = -1, row = 0) {
        if (typeof names === 'string' || typeof names === 'number')
            return _rand(names, lnNo, row);
        assert(names.length > 0, "Should be one name at least");
        return names.map(x => _rand(x, lnNo, row));
    }
    function _atLnNo(name, lnNo, row, fail) {
        let cell = get(name, row, fail);
        if (cell === undefined)
            return undefined;
        let lines = cell.splitTrim(';');
        return lines[lnNo % lines.length];
    }
    function atLnNo(names, lnNo, row = 0, fail = true) {
        if (typeof names === 'string' || typeof names === 'number')
            return _atLnNo(names, lnNo, row, fail);
        assert(names.length > 0, "Should be one name at least");
        return names.map(x => _atLnNo(x, lnNo, row, fail));
    }
    function lines(name, row = 0, fail = true) {
        let cell = get(name, row, fail);
        return cell.split(';');
    }
    return WithIndex(key => get(key, 0, true, true), {
        numOfLines: _dy,
        get: (name, row = 0, fail = false) => get(name, row, fail),
        at: (name, row = 0, fail = true) => get(name, row, fail),
        has: (name, row = 0) => get(name, row, false) !== undefined,
        atLnNo,
        v2i,
        v2d,
        rand_str,
        rand,
        lines,
    });
}
export function parseArgsGrid(str) {
    if (str.trim() === "") {
        return newArgsGrid([
            ["n/a"],
            [undefined]
        ]);
    }
    str = fixCrInCells(str, ';');
    let grid = str
        .splitTrimNoEmpties('\n')
        .map(ln => ln.splitTrim('\t'));
    return newArgsGrid(grid);
}
