import Decimal from "decimal.js";
import Fraction from "fraction.js";
import { assert, log, _l } from "./Logger";
export function isNumeric(value) {
    return /^-?\d+$/.test(value);
}
export function isArrayIndex(str) {
    let indx = Number(str);
    if (isNaN(indx))
        return false;
    if (indx < 0)
        return false;
    return indx === Math.trunc(indx);
}
export const isFraction = (d) => d.isFinite() &&
    !d.toFraction()[1].eq(1);
export function n2d(arg) {
    if (typeof arg === 'number')
        return new Decimal(arg);
    if (!Array.isArray(arg))
        return arg;
    return arg.map(x => typeof x === 'number' ? new Decimal(x) : x);
}
export function d2n(arg) {
    if (typeof arg === 'number')
        return arg;
    if (!Array.isArray(arg))
        return arg.toNumber();
    return arg.map(x => typeof x === 'number' ? x : x.toNumber());
}
export function notFinite2t(d) {
    if (d.isNaN())
        return "NaN";
    if (!d.isFinite())
        return d.lt(0) ? "-oo" : "oo";
    return d2t(d);
}
export function d2t(d, excludeWhole = true) {
    if (!d.isFinite())
        return notFinite2t(d);
    let parts = d.toFraction();
    if (parts[1].eq(1))
        return d.toNumber().toLocaleString('en');
    let fr = new Fraction(d.toNumber());
    return fr.toFraction(excludeWhole);
}
export function d2t5(d, factor, numOfDigits = 2) {
    let maxDenominator = Math.pow(10, numOfDigits);
    let parts = d.toFraction(maxDenominator);
    let num = parts[0].mul(factor).toNumber();
    let den = parts[1].mul(factor).toNumber();
    return `${num}/${den}`;
}
export const nd2f = (n, d) => new Fraction(n.toNumber(), d.toNumber());
export const d2f = (d) => new Fraction(d.toNumber());
export function f2d(fr) {
    if (fr.d === 0)
        return n2d(Infinity);
    return n2d(fr.n).div(fr.d).mul(fr.s);
}
const _t2d = (str) => {
    let t = str
        .trim()
        .replace(/ {2,}/g, ' ')
        .replace(/\+ /g, '+')
        .replace(/\- /g, '-');
    let fr;
    try {
        fr = new Fraction(t);
    }
    catch (err) {
        log.error(`Failed while making fraction from '${str}'`, err, x => _l(x));
        return err.message.includes("by Zero") ?
            n2d(Infinity) : n2d(NaN);
    }
    return f2d(fr);
};
export function t2d(arg) {
    assert(arg !== undefined, x => _l(x));
    return typeof arg === 'string' ?
        _t2d(arg) : arg.map(x => _t2d(x));
}
