import { DFmt } from "../../core/formatters/decimal-format";
import { takeIf } from "../../core/Helpers";
import { assert } from "../../core/Logger";
import { Config } from "../Sq.Config";
import { Formulas } from "../Sq.Formulas";
import { newVariable } from "../Sq.Variable";
let formulas = new Map([
    ["+", (lhs, rhs) => lhs.plus(rhs)],
    ["-", (lhs, rhs) => lhs.minus(rhs)],
    ["*", (lhs, rhs) => lhs.mul(rhs)],
    ["/", (lhs, rhs) => lhs.div(rhs)],
]);
function op_table({ args, genLnNo, nCount }) {
    let _a0b0c0 = args.atLnNo(["a0", "b0", "c0", "v1", "v2"], genLnNo);
    let [fmtA, fmtB, fmtC, fmtV1, fmtV2] = _a0b0c0.map(x => DFmt.parse(x));
    let { maxRetriesOnSameInput } = Config;
    let [a0, b0, c0] = takeIf(maxRetriesOnSameInput, () => args.rand(["a0", "b0", "c0"], genLnNo), arr => arr.hasUniqueValues());
    let [v1, v2] = args.rand(["v1", "v2"], genLnNo);
    let [op1, op2] = args.rand_str(["op1", "op2"], genLnNo);
    let $var = (value, fmt, name) => {
        let view = DFmt.format(value, fmt);
        return newVariable({ name, view, value, fmt });
    };
    let $op = (op, value, fmt, name) => {
        let sOp;
        switch (op) {
            case '+':
                sOp = value.lt(0) ? "" : "+";
                break;
            case '-':
                sOp = value.lt(0) ? "+" : "-";
                break;
            case '/':
                sOp = "-:";
                break;
            case '*':
                sOp = "xx";
                break;
        }
        let sV = DFmt.format(value, fmt);
        let view = `${sOp}${sV}`;
        return newVariable({ name, view, value, fmt });
    };
    let calc = (lhs, op, rhs) => {
        let formula = formulas.get(op);
        assert(formula, `Can't find formula for op='${op}'`);
        return formula(lhs, rhs);
    };
    function makeSequence() {
        let a1 = calc(a0, op1, v1);
        let b1 = calc(b0, op1, v1);
        let c1 = calc(c0, op1, v1);
        let a2 = calc(a1, op2, v2);
        let b2 = calc(b1, op2, v2);
        let c2 = calc(c1, op2, v2);
        return [
            $var(a0, fmtA, "a0"),
            $var(a1, fmtA, "a1"),
            $var(a2, fmtA, "a2"),
            $var(b0, fmtB, "b0"),
            $var(b1, fmtB, "b1"),
            $var(b2, fmtB, "b2"),
            $var(c0, fmtC, "c0"),
            $var(c1, fmtC, "c1"),
            $var(c2, fmtC, "c2"),
        ];
    }
    function makeVariables() {
        return [
            $op(op1, v1, fmtV1, "op1"),
            $op(op2, v2, fmtV2, "op2"),
        ];
    }
    return {
        A: "",
        fmt: fmtA,
        makeSequence,
        makeVariables
    };
}
Formulas.registerFactory('h-table', op_table);
Formulas.registerFactory('v-table', op_table);
