<script lang="ts">
    import Grid from "../core/Grid.svelte";
    import Cell from "./Sq.Cell.svelte"

    import type { SqStore } from "./Sq.Store";

    export let ga: string;
    export let store: SqStore;

    let {sheet} = store;

    function makeLayout() {
        let lines: string[] = [];

        let areas: string = "";
        let cols: string = "";
        let rows: string = "";

        sheet.forEach((line, row) => {
            line.forEach(cell => {
                if (!lines[row]) lines[row] = "";
                lines[row] += `${cell.name} `
            })
            rows += "1fr "
            areas += `"${lines[row]}" `
        });

        if (sheet.length > 0)
            sheet[0].forEach(_ => cols += "1fr ");

        return {areas, cols, rows}
    }
    
    let layout = makeLayout();

</script>

<Grid {layout} 
    ga="{ga}"
    bgColor="bg-gray-100"
    clazz="border border-gray-100"
    style="grid-gap: 1px;"
>
    {#each sheet as line}
    {#each line as cell}
        <Cell on:click {cell} />
    {/each}
    {/each}
</Grid>
