<script lang="ts">

    import { tick } from "svelte";

    import { refreshMath } from "./MathJax.svelte";
    import { delay } from "./Helpers";
    import { pulse } from "./Pulse.svelte";
    import { getBgColor } from './Grid.svelte';
    import { bounceOnDrag } from "./OnDrag.svelte"

    const defaultScale = 1.4;

    export let ga: string;
    export let exp: string;
    export let scale: number = defaultScale;
    export let bgColor: string = getBgColor(ga);
    export let sz: number = 30;

    $: _scale = scale ?? defaultScale;
    $: _sz = sz - _scale * exp.length;
    $: update(exp);

    let _self: HTMLElement;

    async function update(exp: string) {
        await tick();
        _self.innerText = '`' + exp + '`';
        refreshMath();
        await delay(1000);

        _self.querySelectorAll<HTMLElement>("mjx-c").forEach(el => {
            pulse(el);
            bounceOnDrag(el);
        })
    }

</script>

<!-- svelte-ignore a11y-click-events-have-key-events -->
<p 
    class="{bgColor}"
    style="grid-area:{ga};font-size:{_sz}px" 
    bind:this={_self}
    on:click
    use:bounceOnDrag
>
    {exp}
</p>