import { DFmt } from "../../core/formatters/decimal-format";
import { Formulas } from "../Sq.Formulas";
import { newVariable } from "../Sq.Variable";
Formulas.registerFactory(['+(a+)*(b+)', '+(a+)+(b+)'], ({ args, genLnNo, nCount }) => {
    let formula = args.get("formula");
    let op = formula.includes(")*(") ?
        (x, y) => x.mul(y) :
        (x, y) => x.plus(y);
    let _subst = args["subst"];
    let _a0b0 = args.atLnNo(["a0", "pa0", "dpa", "pb0", "dpb"], genLnNo);
    let A = _subst.split(':')[0];
    let [a0, pa0, dpa, pb0, dpb] = args.rand(["a0", "pa0", "dpa", "pb0", "dpb"], genLnNo);
    let fmt = DFmt.parse(_subst, ..._a0b0);
    let $var = (n, a) => {
        let name = `${A}${n}`;
        let view = DFmt.format(a, fmt);
        return newVariable({ name, view, value: a, fmt });
    };
    function makeSequence() {
        let [a, pa, pb] = [a0, pa0, pb0];
        let seq = [$var(0, a)];
        for (let n = 1; n <= nCount; n++) {
            if (n % 2 === 1) {
                a = a.plus(pa);
                pa = pa.plus(dpa);
            }
            else {
                a = op(a, pb);
                pb = pb.plus(dpb);
            }
            seq.push($var(n, a));
        }
        return seq;
    }
    function makeVariables() {
        return [];
    }
    return {
        A,
        fmt,
        makeSequence,
        makeVariables
    };
});
