import { DFmt } from "../../core/formatters/decimal-format";
import { assert, log, _l } from "../../core/Logger";
import { Formulas } from "../Sq.Formulas";
import { newVariable } from "../Sq.Variable";
function makeLambdas({ args, genLnNo, options, fmt }) {
    function getB() {
        let b = args.rand("b", genLnNo);
        let sB = DFmt.format(b, fmt);
        return [b, sB];
    }
    function getPmOpB() {
        let b = args.rand("b", genLnNo);
        let op = b.lt(0) ? "-" : "+";
        let sB = DFmt.format(b.abs(), fmt);
        return [b, `${op} ${sB}`];
    }
    function parseHowFmt() {
        let how = "as-is";
        switch (options.get("how")) {
            case "colored":
                how = "colored";
                break;
            case "squares":
                how = "squares";
                break;
        }
        return how;
    }
    let howFmt = parseHowFmt();
    let G = howFmt === "squares" ? _ => `color(green)(square)` :
        howFmt === "colored" ? x => `color(green)(${x})` :
            x => x;
    let R = howFmt === "squares" ? _ => `color(red)(square)` :
        howFmt === "colored" ? y => `color(red)(${y})` :
            y => y;
    let factories = {
        "(x,y)=>x+y": () => ({
            apply: (x, y) => x.add(y),
            how: (x, y) => `${G(x)} + ${R(y)}`
        }),
        "(x,y)=>x-y": () => ({
            apply: (x, y) => x.minus(y),
            how: (x, y) => `${G(x)} - ${R(y)}`
        }),
        "(x,y)=>y-x": () => ({
            apply: (x, y) => y.minus(x),
            how: (x, y) => `${R(y)} - ${G(x)}`
        }),
        "(x,y)=>x+y+b": () => {
            let [b, opB] = getPmOpB();
            return {
                apply: (x, y) => x.add(y).plus(b),
                how: (x, y) => `${G(x)} + ${R(y)} ${opB}`
            };
        },
        "(x,y)=>x-y+b": () => {
            let [b, opB] = getPmOpB();
            return {
                apply: (x, y) => x.minus(y).plus(b),
                how: (x, y) => `${G(x)} - ${R(y)} ${opB}`
            };
        },
        "(x,y)=>y-x+b": () => {
            let [b, opB] = getPmOpB();
            return {
                apply: (x, y) => y.minus(x).plus(b),
                how: (x, y) => `${R(y)} - ${G(x)} ${opB}`
            };
        },
        "(x,y)=>x*y": () => ({
            apply: (x, y) => x.mul(y),
            how: (x, y) => `${G(x)} xx ${R(y)}`
        }),
        "(x,y)=>x/y": () => ({
            apply: (x, y) => x.div(y),
            how: (x, y) => `${G(x)} -: ${R(y)}`
        }),
        "(x,y)=>y/x": () => ({
            apply: (x, y) => y.div(x),
            how: (x, y) => `${R(y)} -: ${G(x)}`
        }),
        "(x,y)=>(x+y)*b": () => {
            let [b, sB] = getB();
            return {
                apply: (x, y) => x.plus(y).mul(b),
                how: (x, y) => `(${G(x)} + ${R(y)}) xx ${sB}`
            };
        },
        "(x,y)=>(x-y)*b": () => {
            let [b, sB] = getB();
            return {
                apply: (x, y) => x.minus(y).mul(b),
                how: (x, y) => `(${G(x)} - ${R(y)}) xx ${sB}`
            };
        },
        "(x,y)=>(y-x)*b": () => {
            let [b, sB] = getB();
            return {
                apply: (x, y) => y.minus(x).mul(b),
                how: (x, y) => `(${R(y)} - ${G(x)}) xx ${sB}`
            };
        },
        "(x,y)=>(x+y)/b": () => {
            let [b, sB] = getB();
            return {
                apply: (x, y) => x.plus(y).div(b),
                how: (x, y) => `(${G(x)} + ${R(y)}) -: ${sB}`
            };
        },
        "(x,y)=>(x-y)/b": () => {
            let [b, sB] = getB();
            return {
                apply: (x, y) => x.minus(y).div(b),
                how: (x, y) => `(${G(x)} - ${R(y)}) -: ${sB}`
            };
        },
        "(x,y)=>(y-x)/b": () => {
            let [b, sB] = getB();
            return {
                apply: (x, y) => y.minus(x).div(b),
                how: (x, y) => `(${R(y)} - ${G(x)}) -: ${sB}`
            };
        },
        "(x,y)=>x*y+b": () => {
            let [b, opB] = getPmOpB();
            return {
                apply: (x, y) => x.mul(y).plus(b),
                how: (x, y) => `${G(x)} xx ${R(y)} ${opB}`
            };
        },
        "(x,y)=>x/y+b": () => {
            let [b, opB] = getPmOpB();
            return {
                apply: (x, y) => x.div(y).plus(b),
                how: (x, y) => `${G(x)} -: ${R(y)} ${opB}`
            };
        },
        "(x,y)=>y/x+b": () => {
            let [b, opB] = getPmOpB();
            return {
                apply: (x, y) => y.div(x).plus(b),
                how: (x, y) => `${R(y)} -: ${G(x)} ${opB}`
            };
        },
    };
    function fromStr(name) {
        let factory = factories[name];
        assert(factory, `Can't find lambda definition for '${name}'`);
        return factory();
    }
    return {
        fromStr,
        get: (name) => fromStr(name)
    };
}
Formulas.registerFactory('lambda', ({ args, genLnNo, options }) => {
    let def = args.at("lambda");
    let _x0y0 = args.atLnNo(["x0", "y0", "y1", "y2", "y3"], genLnNo);
    let fmt = DFmt.parse(_x0y0);
    let [x0, y0, y1, y2, y3] = args.rand(["x0", "y0", "y1", "y2", "y3"], genLnNo);
    let lambdas = makeLambdas({ args, genLnNo, options, fmt });
    let lambda = log.onerror(() => lambdas.fromStr(def), ex => _l(ex, { def, _x0y0, fmt }));
    let x1 = lambda.apply(x0, y0);
    let x2 = lambda.apply(x1, y1);
    let x3 = lambda.apply(x2, y2);
    let x4 = lambda.apply(x3, y3);
    let $var = (value, name) => {
        let view = DFmt.format(value, fmt);
        return newVariable({ name, view, value, fmt });
    };
    let how = (n, x, y) => {
        let name = `how${n}`;
        let [sX, sY] = DFmt.formats([x, y], fmt);
        let view = lambda.how(sX, sY);
        return newVariable({ name, view, value: x, fmt });
    };
    function makeSequence() {
        return [
            $var(x0, "x0"),
            $var(x1, "x1"),
            $var(x2, "x2"),
            $var(x3, "x3"),
            $var(x4, "x4"),
            $var(y0, "y0"),
            $var(y1, "y1"),
            $var(y2, "y2"),
            $var(y3, "y3"),
        ];
    }
    function makeVariables() {
        return [
            how(1, x0, y0),
            how(2, x1, y1),
            how(3, x2, y2),
            how(4, x3, y3),
        ];
    }
    return {
        A: "",
        fmt,
        makeSequence,
        makeVariables
    };
});
