import { safe } from "./Helpers";
let Null = {
    markCurrentCard: () => { },
    isCurrentCardMarked: () => false
};
function newAnkiDroid() {
    function init() {
        return false && safe(() => {
            AnkiDroidJS.init(JSON.stringify({
                version: "0.0.1",
                developer: "info@gebemot.com"
            }));
            return true;
        }, false);
    }
    function markCard() {
        if (AnkiDroidJS.ankiGetCardMark())
            return;
        ankiMarkCard();
    }
    if (!init())
        return Null;
    return {
        markCurrentCard: () => safe(() => markCard()),
        isCurrentCardMarked: () => safe(() => AnkiDroidJS.ankiGetCardMark())
    };
}
;
export const ankiDroid = newAnkiDroid();
