<script context="module" lang="ts">

    let callbacks: any = {};

    export function onAny(name: string, cb: (args: any) => void): void {
        let funcs = callbacks[name] ?? [];
        funcs.push(cb);
        callbacks[name] = funcs;
    }

    export function fireAny(name: string, args: any): void {
        let funcs = callbacks[name] ?? [];
        funcs.forEach(func => 
            setTimeout(() => func(args), 0));
    }

</script>
