import Decimal from "decimal.js";
import { throwError } from "../Logger";
Array.prototype.str = function (sep, v2s) {
    sep ??= ',';
    let all = v2s ? this.map(x => v2s(x)) : this;
    return `[${all.join(sep)}]`;
};
Array.prototype.take = function (count) {
    return this.slice(0, count);
};
Array.prototype.removeDuplicates = function (compare) {
    compare ??= (lhs, rhs) => lhs === rhs;
    if (compare.length === 1)
        throwError(`Compare by key is not implemented yet`);
    var uniqueElements = [];
    this.forEach(elem => {
        for (let unique of uniqueElements) {
            if (compare(elem, unique))
                return;
        }
        uniqueElements.push(elem);
    });
    return uniqueElements;
};
Array.prototype.hasUniqueValues = function (compare) {
    let me = this;
    if (me.length === 0)
        return true;
    compare ??= me[0] instanceof Decimal ?
        (x, y) => x.eq(y) :
        (lhs, rhs) => lhs === rhs;
    if (compare.length === 1)
        throwError(`Compare by key is not implemented yet`);
    for (let i = 0; i < me.length; i++) {
        for (let j = i + 1; j < this.length; j++) {
            if (compare(me[i], me[j]))
                return false;
        }
    }
    return true;
};
