import { newCellAsRandomGenerator } from "../../core/CellAsRandomGenerator";
import { DFmt } from "../../core/formatters/decimal-format";
import { t2d } from "../../core/Helpers.convert";
import { Formulas } from "../Sq.Formulas";
import { newVariable } from "../Sq.Variable";
Formulas.registerFactory('a0+nd', ({ args, genLnNo, nCount }) => {
    let _subst = args["subst"];
    let _a0 = args.atLnNo("a0", genLnNo);
    let _d = args.atLnNo("d", genLnNo);
    let _dd = args.atLnNo("dd", genLnNo, 0, false) ?? "0";
    let _ddGen = newCellAsRandomGenerator(_dd, genLnNo);
    let A = _subst.split(':')[0];
    let a0 = args.rand("a0", genLnNo);
    let d = args.rand("d", genLnNo);
    let dd = t2d(_ddGen.one());
    let op = _ddGen.op;
    let fmt = DFmt.parse(_subst, _a0, _d);
    let dView = DFmt.format(d, fmt);
    let ddView = DFmt.format(dd, fmt);
    let $var = (n, a) => {
        let name = `${A}${n}`;
        let view = DFmt.format(a, fmt);
        return newVariable({ name, view, value: a, fmt });
    };
    function makeSequence() {
        let a = a0;
        let diff = d;
        let seq = [$var(0, a)];
        for (let n = 1; n <= nCount; n++) {
            a = a.plus(diff);
            seq.push($var(n, a));
            diff = op === "*" ?
                diff.mul(dd) :
                diff.plus(dd);
        }
        return seq;
    }
    function makeVariables() {
        return [
            newVariable({ name: `d${A}`, view: dView, value: d, fmt }),
            newVariable({ name: `dd${A}`, view: ddView, value: dd, fmt }),
        ];
    }
    return {
        A,
        fmt,
        makeSequence,
        makeVariables
    };
});
