<script context="module" lang="ts">

    import { randId } from "./Helpers.rand";


    interface Point {
        x: number;
        y: number;
    }

    let str = p => `[${p.x}x${p.y}]`;

    export interface DragArgs {
        id: string,
        start: Point,
        offset: Point
    }

    export interface DragEvents {
        "drag:start": DragArgs,
        "drag:end": DragArgs,
        "drag:move": DragArgs,
        "drag:other": {}
    }

    export type EventName = keyof(DragEvents);

    interface Config {
        id: string;
        snapTo: [number, number];
    }

    type Options = Partial<Config>;

    let _activeDrags: Set<HTMLElement> = new Set();

    export function beingDragged(elem: HTMLElement) {
        return _activeDrags.has(elem);
    }

    export function handleDragEvents(elem: HTMLElement, options?: Options) {

        let _config: Config = {
            id: options?.id ?? randId(5),
            snapTo: options?.snapTo ?? [1, 1]
        }

        let _myId = -1;
        let _start: Point = {x: 0, y: 0};
        let _last: Point = {x: 0, y: 0};

        let _skippedMoves: number = 0;
        let _moves: number = 0;

        function fire(name: EventName, args?: DragArgs) {
            switch (name) {
                case "drag:start":
                    _activeDrags.add(elem);
                    break;
                case "drag:end":
                    _activeDrags.delete(elem);
                    break;
            }
            elem.dispatchEvent(
                new CustomEvent(name, { detail: args })
            );
        }        

        function makeArgs(touch: Touch, start?: Point): DragArgs {
            let pnt: Point = {
                x: (.5 + touch.clientX) | 0,
                y: (.5 + touch.clientY) | 0,
            }

            start ??= pnt;

            let offset: Point = {
                x: pnt.x - start.x,
                y: pnt.y - start.y
            }

            return {
                id: _config.id,
                start, offset
            }
        }

        function snapToGrid(args: DragArgs, last: Point): boolean {
            let sameCell = 
                Math.abs(last.x - args.offset.x) < _config.snapTo[0] &&
                Math.abs(last.y - args.offset.y) < _config.snapTo[1];

            if (sameCell) return false;

            args.offset.x -= args.offset.x % _config.snapTo[0]
            args.offset.y -= args.offset.y % _config.snapTo[1]

            return true;
        }

        function onTouchStart(ev: TouchEvent) {
            if (_myId !== -1)
                return fire("drag:other");

            let touch = ev.changedTouches[0];
            let args = makeArgs(touch);

            _myId = touch.identifier;
            _start = args.start;
            _last = args.offset;
            _moves = 0;
            _skippedMoves = 0;

            // console.log(`touch start on ${_config.id} (${_myId}) at ${str(_start)}`);

            fire("drag:start", args);
        }

        function onTouchMove(ev: TouchEvent) {
            let touch = ev.changedTouches[0];

            if (_myId !== touch.identifier)
                return _skippedMoves++;

            let args = makeArgs(touch, _start);

            if (!snapToGrid(args, _last))
                return _skippedMoves++;
            
            _last = args.offset;
            _moves++;

            fire("drag:move", args);
        }

        async function onTouchEnd(ev: TouchEvent) {
            let touch = ev.changedTouches[0];

            if (_myId !== touch.identifier)
                return fire("drag:other");

            let args = makeArgs(touch, _start);

            // console.log(`touch end on ${_config.id} (${_myId}) at ${str(args.offset)} moves=${_moves} (skipped ${_skippedMoves})`);

            _myId = -1;

            fire("drag:end", args);
        }

        elem.addEventListener("touchstart", onTouchStart, {passive: true});
        elem.addEventListener("touchmove", onTouchMove, {passive: true});
        elem.addEventListener("touchend", onTouchEnd, {passive: true});
        elem.addEventListener("touchcancel", onTouchEnd, {passive: true});

        return {
            destroy() {
                elem.removeEventListener("touchstart", onTouchStart);
                elem.removeEventListener("touchmove", onTouchMove);
                elem.removeEventListener("touchend", onTouchEnd);
                elem.removeEventListener("touchcancel", onTouchEnd);
            }
        }
    }

</script>