<script lang="ts">

    import { pulse } from '../core/Pulse.svelte';
    import { bounceOnDrag } from "../core/OnDrag.svelte";
    
    import {on, fire,} from './Model.svelte'
    import type {Game} from './Model.svelte'

    let _text: string
    let _class: string
    let _scale: number
    let _sz: number;

    export let ga: string;
    export let index: number
    export let sz: number = 30

    function onClick() {
        fire("--evt-answer", {index});
    }

    function getText(game: Game): string {
        if (game.state === "first-click")
            return "??";

        return game.answers[index].toLocaleString('en');
    }

    function getClass(game: Game) {
        if (game.state === "first-click")
            return "bg-gray-100";

        if (game.state === "active")
            return "bg-orange-200";

        if (game.rightAt === index)
            return "bg-green-200";

        if (game.clickedAt !== index)
            return "bg-gray-100";

        return "bg-red-200";
    }

    function updateView(game: Game) {
        _text = getText(game);
        _class = getClass(game);

        _scale = _text.length <= 3 ? 1.2 : 2.0;
        _sz = sz - _scale * _text.length;
    }

    on("--cmd-update-views", updateView);

</script>

<button 
    class={_class}
    style="grid-area:{ga};font-size:{_sz}px" 
    on:click={onClick}
    use:pulse
    use:bounceOnDrag
>
    {_text}
</button>