import { formatG2l } from "./g2l-formatter";
export function shekel_agorot(value, fmt) {
    let variations = formatG2l(value, fmt, {
        gu: 'שקל',
        lu: 'אגורות',
        g2l: x => x.mul(100),
        luLimit: 300
    });
    let noShekels = variations.filter(x => !x.includes('שקל'));
    switch (fmt) {
        case "$": return variations;
        case "$1": return variations;
        case "$2":
            return noShekels.length > 0 ?
                noShekels : variations;
    }
}
