<script context="module" lang="ts">
    import { run } from "./Helpers";
    import { randFrom } from "./Helpers.rand";



    
    const names = ["yes", "no", "done", "idle", "sound-on", "sound-off", 
        "on-drag-start", "on-drag-end", "time-is-up"] as const;

    export type Mp3Name = typeof names[number];

    let _library: Map<Mp3Name, HTMLAudioElement[]> = new Map();
    let _soundOn: boolean = true;

    function loadFrom(assetsFolder: string) {

        function loadNext(folder: string, name: Mp3Name, no: number) {
            let fileName = folder === "" ?
                `${name}-${no}.mp3` :
                `${folder}/${name}-${no}.mp3`;

            let audio = new Audio(fileName);

            audio.addEventListener("canplaythrough", _ => {
                // console.log(`loaded: ${fileName}`);
                let tracks = _library.get(name);
                tracks.push(audio);
                run(() => loadNext(folder, name, no + 1));
            }, {once: true});
        }

        names.forEach(name => {
             // console.log(`loading tracks of '${name}'`);
            _library.set(name, []);
            run(() => loadNext(assetsFolder, name, 1))
        });
    }

    interface IMp3 {
        play(name: Mp3Name);
        buildMp3Library(assetsFolder: string, soundOn: boolean);
        soundSwitch(soundOn: boolean);
        playRandom();
        readonly soundIsOn: boolean;
    }

    function play(name: Mp3Name) {
        let tracks = _library.get(name);

        if (!tracks || tracks.length === 0) return;

        let track = randFrom(tracks);

        track.play().catch(err => {});
    }

    class CMp3 implements IMp3 {
        play(name: Mp3Name) {
            if (!_soundOn) return;

            play(name);
        }

        buildMp3Library(assetsFolder: string, soundOn: boolean) {
            _soundOn = soundOn;
            run(() => loadFrom(""));
            run(() => loadFrom(assetsFolder));
        }

        soundSwitch(soundOn: boolean) {
            if (_soundOn === soundOn) return;

            play(soundOn ? "sound-on" : "sound-off");
            _soundOn = soundOn;
        }

        playRandom() {
            if (!_soundOn) return;

            play(randFrom(names));
        }

        get soundIsOn() {
            return _soundOn;
        }
    }

    export const Mp3: IMp3 = new CMp3();

</script>