export const run = (task) => setTimeout(task, 0);
export const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));
const _range = (start, end) => Array.from({ length: (end - start) }, (_, k) => k + start);
// NOTE: range is not inclusive
export const range = (start, end) => end ? _range(start, end) : _range(0, start);
export function safe(func, errRet = undefined) {
    try {
        return func();
    }
    catch (err) {
        let name = func.name;
        if (name === "")
            name = "unknown";
        log.error(`Error in ${name}`, err, x => _l(x));
        return errRet;
    }
}
export const splitNoEmpty = (str, delim) => str.split(delim ?? " ").filter(Boolean).map(x => x.trim());
export function allAreUnique(arr) {
    let set = new Set();
    for (let x of arr) {
        let str = `${x}`;
        if (set.has(str))
            return false;
        set.add(str);
    }
    return true;
}
export const vibrateSOS = () => navigator.vibrate([100, 30, 100, 30, 100, 30, 200, 30, 200, 30, 200, 30, 100, 30, 100, 30, 100]);
export function one2many(x) {
    return x === undefined ? [] :
        Array.isArray(x) ? x : [x];
}
export function fixCrInCells(str, replaceWith) {
    return str.replaceAll(/\"([\s\S]*?)\"/g, (_, cell) => cell
        .splitTrim('\n')
        .join(replaceWith));
}
function toEnum(def, name) {
    return name in def ?
        name :
        throwError(`Unknown name '${name}'`, x => _l(x));
}
export function takeIf(maxLoops, factory, validate) {
    let variant = factory();
    for (let count = maxLoops - 1; count-- > 0;) {
        if (validate(variant))
            return variant;
        variant = factory();
    }
    log.error(`Can't find a satisfying variant (maxLoops=${maxLoops})`);
    return variant;
}
