<script context="module" lang="ts">

    import type { Decimal } from "decimal.js"

    import { onAny, fireAny } from "./../core/IoBus.svelte"
    import { CTimedCmd } from "./../core/TimedCmd.svelte"
    import type { ITimedCmd } from "./../core/TimedCmd.svelte"

    export type GameType = 
        "equ+-" | "equ*/" | "ab+c=d" | "a*/(b+c)=d" | "a/b=c/d" |
        "%-simple" | "%-plus" | "%-minus" | "%-all" | "%-a-of-c" | "%-b-is-c" |
        "fr+-" | "fr+-ab" | "fr-reduction" | "fr*/" |
        "dividers" | "dividers-2" | "dividers+-" |
        "+-dots";

    export interface Round {
        seconds: number;
        gameType: GameType;
        progress: number[];
        equ: string;
        mathScale: number;
        rightAns: Decimal;
        answers: Decimal[];
    }

    export interface GameState {
        "start": {};
        "clicked": {};
        "answered": {};
        "right": {};
        "wrong": {};
    }

    export interface GameEvent {
        "ans-click": number;
        "equ-click": string;
        "clock-click": boolean;
        "progress-click": number;
        "grid-click": {};
    }

    export interface Config {
        rounds: number;
    }

    export interface Msgs {
        "--cmd-new-round": {}
        "--evt-click" : {}
        "--evt-yes": {},
        "--evt-no": {},
        "--evt-done": {}
        "--cmd-idle": {}
        "--evt-time-is-up": {}
    }

    export interface Script {
        readonly abc: Decimal[];
        readonly answers: Decimal[];
        readonly equ: string;
    }

    export function on<Name extends keyof(Msgs)>(name: Name, cb: (args: Msgs[Name]) => void): void {
        onAny(name, cb);
    }

    export function fire<Name extends keyof(Msgs)>(name: Name, args?: Msgs[Name]): void {
        fireAny(name, args);
    }

    export function newTimedCmd(cmd: keyof Msgs, timeout: number, jitter?: number): ITimedCmd {
        return new CTimedCmd(cmd, timeout, jitter ?? 0);
    }

    export function parseCard(cmds: Iterable<string[]>) {

    }

</script>