import { toAscii } from "../AsciiMathUtils";
import { n2d } from "../Helpers.convert";
import { formatG2l } from "./g2l-formatter";
export function kilo(value, fmt) {
    let kilos = () => `${d.div(1000)} ק"ג`;
    let grams = () => `${value} גרם`;
    if (value.includes("/"))
        return [toAscii(value)];
    let d = n2d(parseFloat(value));
    if (d.isNaN())
        return [];
    if (d.gte(2000))
        return [kilos()];
    return [
        grams(),
        kilos()
    ];
}
export const kg_gram = (value, fmt) => formatG2l(value, fmt, {
    gu: 'ק"ג',
    lu: 'גרם',
    g2l: x => x.mul(1000),
    luLimit: 3000
});
