<script context="module" lang="ts">

    import { Decimal } from "decimal.js"
    import Fraction from "fraction.js";

    import { Anki } from "../core/Anki";
    import { rand, shuffle } from "../core/Helpers.rand";

    import type { Config, Round } from "./Model.svelte"

    interface IGame {
        isLast(): boolean;
        addRounds(count: number);
        nextRound(): Round;
    }

    let _config = Anki.getConfig<Config>();
    let _fractions: Fraction[];
    let _next: number;
    let _totalRounds = _config.rounds;

    let n2d = (n: number) => new Decimal(n);
    let s2f = (str: string) => new Fraction(str);

    function fxd(n: number, digits: number) {
        let p = n2d(10).pow(digits);
        return n2d(n).mul(p).trunc().div(p);
    }

    function aTriple(fr: Fraction, abc: number[]): string[] {
        let n = fr.valueOf();
        let dots = n.toFixed(5).length < n.toString().length;

        let triple = [
            fr.toFraction(false),
            fxd(n, 4).toString() + (dots ? "..." : ""),
            fr.mul(100).toString(2) + '%'
        ];

        return [                // shuffle triple in a same order for all triples in a round
            triple[abc[0]],
            triple[abc[1]],
            triple[abc[2]]
        ];
    }


    class CGame implements IGame {
        constructor() {
            function asFractions(lhs: Fraction, rhs: Fraction): number {
                return lhs.compare(rhs);
            }

            for (let cmd of Anki.commands()) {
                switch (cmd[0].toLocaleLowerCase()) {
                }
            }

            _fractions = Anki
                .tokens()
                .map(t => s2f(t))
                .sort(asFractions);

            _next = -1;
        }

        isLast(): boolean {
            return _next + 1 >= _totalRounds;
        }

        addRounds(count: number) {
            _totalRounds += count;
        }

        nextRound(): Round {
            _next++;

            // assert _fractions.length >= 4
            let firstAt = rand(0, _fractions.length - 4);

            let abc = shuffle([0, 1, 2]);

            let triples = shuffle([0, 1, 2, 3].map(i => 
                aTriple(_fractions[firstAt + i], abc)));

            let fr = triples[0][0];

            let answers = shuffle([
                triples[0][1],
                triples[1][1],
                triples[2][2],
                triples[3][2],
            ]);

            let rightAt = answers.findIndex(
                ans => ans === triples[0][1]);

            let progress = [
                _next < _totalRounds ? _next : _totalRounds,
                _totalRounds
            ];

            return {
                fr, rightAt, answers,
                progress
            }
        }
    }

    export function newFrPairs(): IGame {
        return new CGame();
    }

</script>