<script lang="ts">
    import { pulse } from '../core/Pulse.svelte';
    import { bounceOnDrag } from "../core/OnDrag.svelte"

    import type { SqStore } from './Sq.Store'

    export let ga: string;
    export let store: SqStore["answers"][0];

    let {value, isClicked, isRight} = store;

    $: bgColor = !$isClicked ? "bg-gray-100" :
        isRight ? "bg-green-200" : "bg-red-200";

</script>


<button
    class="{bgColor}"
    style="grid-area:{ga};"
    use:pulse
    use:bounceOnDrag
    on:click
>
    `{value}`
</button>
