// WARN: rand(min, max) is inclusive   min <= result <= max
export function _rand(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max + 1);
    return Math.floor(Math.random() * (max - min) + min);
}
export function rand(prm0, prm1) {
    return typeof prm0 === 'number' ?
        prm1 !== undefined ?
            _rand(prm0, prm1) :
            _rand(0, prm0) :
        Array.isArray(prm0) ?
            _rand(prm0[0], prm0[1]) :
            _rand(prm0.min, prm0.max);
}
export function randFrom(arr) {
    return arr[_rand(0, arr.length - 1)];
}
export function randFromIf(arr, iff) {
    let filtered = arr.filter(iff);
    // assert there is any number in filtered array
    return filtered[_rand(0, filtered.length - 1)];
}
export const trueFalse = () => _rand(0, 1) == 0;
export const randSign = () => _rand(0, 1) == 0 ? +1 : -1;
export function pm(num) {
    let plusOrMinus = (_rand(0, 1) == 0 ? +1 : -1);
    return typeof num === 'number' ?
        num * plusOrMinus :
        num.mul(plusOrMinus);
}
export function randId(len, charSet) {
    charSet ??= 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var str = '';
    for (var i = 0; i < len; i++) {
        var indx = Math.floor(Math.random() * charSet.length);
        str += charSet.charAt(indx);
    }
    return str;
}
export function shuffle(src) {
    let dst = [...src];
    for (let i = dst.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [dst[i], dst[j]] = [dst[j], dst[i]];
    }
    return dst;
}
