<script context="module" lang="ts">

    import { Decimal } from "decimal.js"

    import { Anki } from "../core/Anki"
    import { allAreUnique } from "../core/Helpers";
    import { nd2f, f2d, d2f, n2d, d2n, d2t } from "../core/Helpers.convert";
    import { pm, rand, randFrom, shuffle, randFromIf, trueFalse } from "../core/Helpers.rand";

    import type { Round, Config, GameType, Script } from "./Model.svelte"
    import { get, makeProduct, parseDecimal, percentsMinus, percentsPlus, percentsSimple } from "./Utils.svelte"

    
    interface IGame {
        isLast(round: Round): boolean;
        addRounds(count: number);
        nextRound(): Round;
    }

    let _config = Anki.getConfig<Config>();
    let _patterns: string[];
    let _next: number;
    let _totalRounds = _config.rounds;
    let _gameType: GameType = "equ+-";
    let _a: number[] = [10, 90];
    let _b: number[] = [10, 90];
    let _c: number[] = [10, 90];
    let _d: number[] = [10, 90];
    let _n: number[] = [1, 1];
    let _aA: Decimal[] = [];
    let _bA: Decimal[] = [];
    let _cA: Decimal[] = [];
    let _dA: Decimal[] = [];
    let _nA: Decimal[] = [];
    let _x: string[] = ['X'];
    let _noOverflow: boolean = false;
    let _properOnly: boolean = false;
    let _seconds: number = 15 * _config.rounds;
    let _mathScale: number;
    let _noAbSwap: boolean = false;
    let _ansDividers: Decimal[] = [];
    let _dotsFactor: number = 1;

    
    function newAbc(pattern: string): Decimal[] {

        let tZero = n => n - n % 10;

        if (_gameType === "+-dots") {
            let [a, b] = [get(_a, _aA), get(_b, _bA)];

            let a0 = rand(1, 9);
            let b0 = _noOverflow ? rand(0, 9 - a0) : rand(10 - a0, 9);
            let aa = n2d(tZero(a) + a0).div(_dotsFactor);
            let bb = n2d(tZero(b) + b0).div(_dotsFactor);

            let f = n2d(rand(1, 10));
            aa = aa.plus(f.div(_dotsFactor).div(10));

            if (trueFalse()) 
                bb = bb.plus(n2d(10).minus(f).div(_dotsFactor).div(10));

            let c  = aa.plus(bb);
            return [
                ...shuffle([aa, bb]), 
                c
            ];
        }

        if (_gameType === "equ+-") {
            let [a, b] = [get(_a, _aA), get(_b, _bA)];

            let a0 = rand(1, 9);
            let b0 = _noOverflow ? rand(0, 9 - a0) : rand(10 - a0, 9);
            let aa = n2d(tZero(a) + a0);
            let bb = n2d(tZero(b) + b0);
            let c  = aa.plus(bb);

            return [
                ...shuffle([aa, bb]), 
                c
            ];
        }

        if (_gameType === "equ*/") {
            let [a, b] = [get(_a, _aA), get(_b, _bA)];

            let c = a.mul(b);
            let ab = _noAbSwap ? [a, b] : shuffle([a, b]);

            return [
                ...ab,
                c
            ];
        }

        if (_gameType === "ab+c=d") {
            let [a, b] = [get(_a, _aA), get(_b, _bA)];
            let d = get(_d, _dA);

            let c = d.minus(a.mul(b));

            return [
                ...shuffle([a, b]), 
                c, d
            ];
        }

        if (_gameType === "a*/(b+c)=d") {
            for (let loopLimit = 100; true; ) {
                let [a, b] = [get(_a, _aA), get(_b, _bA)];
                let c = get(_c, _cA);
                let d: Decimal;

                if (pattern.includes('+')) {
                    d = a.mul(b).plus(a.mul(c));
                } else {
                    if (b.lt(c))
                        [b, c] = [c, b];
                    d = a.mul(b).minus(a.mul(c));
                }

                if (pattern.includes('/') && d.eq(0) && loopLimit-- > 0)
                    continue;

                if (pattern.includes('/'))
                    [a, d] = [d, a];

                return [a, b, c, d];
            }
        }

        if (_gameType === "a/b=c/d") {
            for (let loopLimit = 100; true; ) {
                let [a, b] = [get(_a, _aA), get(_b, _bA)];

                let c = get(_c, _cA);
                let n1 = get(_n, _nA);
                let n2 = get(_n, _nA);

                if (a.gt(b) && _properOnly)
                    [a, b] = [b, a];

                if (a.eq(b) && loopLimit-- > 0)
                    continue;

                return [
                    a.mul(n1), 
                    b.mul(n1),
                    a.mul(c).mul(n2), 
                    b.mul(c).mul(n2)
                ];
            }
        }

        if (percentsSimple(_gameType, pattern)) {
            let b = get(_b, _bA);
            let c: Decimal;
            let a: Decimal;
            let loopLimit = 100;
            do {
                c = get(_c, _cA);
                a = c.mul(100).div(b);
                if (--loopLimit === 0) break;
            } while (!a.trunc().eq(a));

            if (loopLimit === 0)
                console.log(d2n([a, b, c]));

            return [a, b, c];
        }
        else
        if (percentsPlus(_gameType, pattern)) {
            // assert _bA is here
            let b = randFromIf(_bA, x => x.gte(100));
            let c: Decimal;
            let a: Decimal;

            let loopLimit = 100;
            do {
                c = get(_c, _cA);
                a = c.mul(100).div(b);
                if (--loopLimit === 0) break;
            } while (!a.trunc().eq(a));

            b = b.minus(100);

            if (loopLimit === 0)
                console.log(d2n([a, b, c]));

            return [a, b, c];
        }
        else
        if (percentsMinus(_gameType, pattern)) {
            // assert _bA is here
            let b = randFromIf(_bA, x => x.lt(100));
            let c: Decimal;
            let a: Decimal;

            let loopLimit = 100;
            do {
                c = get(_c, _cA);
                a = c.mul(100).div(b);
                if (--loopLimit === 0) break;
            } while (!a.trunc().eq(a));

            if (loopLimit === 0)
                console.log(d2n([a, b, c]));

            b = n2d(100).minus(b);

            return [a, b, c];
        }

        if (_gameType === "%-a-of-c") {
            let b = get(_b, _bA);
            let c: Decimal;
            let a: Decimal;
            let loopLimit = 100;
            do {
                c = get(_c, _cA);
                a = b.mul(c).div(100);
                if (--loopLimit === 0) break;
            } while (!a.trunc().eq(a));

            if (loopLimit === 0)
                console.log(d2n([a, b, c]));

            return [a, b, c];
        }

        if (_gameType === "%-b-is-c") {
            let b = get(_b, _bA);
            let c: Decimal;
            let a: Decimal;
            let loopLimit = 100;
            do {
                a = get(_a, _aA);
                c = a.mul(b).div(100);
                if (--loopLimit === 0) break;
            } while (!c.trunc().eq(c));

            if (loopLimit === 0)
                console.log(d2n([a, b, c]));

            return [a, b, c];
        }

        if (_gameType === "fr+-") {
            let [a, b] = [get(_a, _aA), get(_b, _bA)];
            let c = a.plus(b);

            return [
                ...shuffle([a, b]), 
                c
            ];
        }

        if (_gameType === "fr*/") {
            let [a, b] = shuffle([get(_a, _aA), get(_b, _bA)]);

            let c = pattern.includes('*') ?
                a.mul(b) : a.div(b);

            return [a, b, c];
        }

        if (_gameType === "fr+-ab") {
            let [a, b] = [get(_a, _aA), get(_b, _bA)];

            if (a.eq(b)) {
                return pattern.includes('+') ?
                    [a, b, a.plus(b)] :
                    [a.plus(b), ...shuffle([b, a])];
            }

            if (pattern.includes('+')) {
                return [...shuffle([a, b]), a.plus(b)];
            } else {
                let lhs = Decimal.max(a, b);
                let rhs = Decimal.min(a, b);
                let c = lhs.minus(rhs);
                return [lhs, rhs, c];
            }
        }

        if (_gameType === "fr-reduction") {
            for (let loopLimit = 20; true; ) {
                let [a, b] = [get(_a, _aA), get(_b, _bA)];
                let d = get(_d, _dA);
                a = a.mul(d);
                b = b.mul(d);
                let fr = nd2f(a, b);
                let c = f2d(fr);

                if (a.gte(b) && _properOnly && loopLimit-- > 0)
                    continue;

                return [a, b, c];
            }
        }

        if (_gameType === "dividers") {
            do {
                let [a, aDividers] = makeProduct(_a[0], _a[1], _nA);
                let [b, bDividers] = makeProduct(_b[0], _b[1], _nA);

                if (a.eq(b)) continue;

                _ansDividers = [...aDividers, ...bDividers];

                let [c] = makeProduct(_c[0], _c[1], _ansDividers);

                if (c.eq(a) || c.eq(b)) continue;

                return [a, b, c];
            } 
            while (true);
        }

        if (_gameType === "dividers-2") {
            let abc: Decimal[];
            for (let loopLimit = 50; loopLimit-- > 0; ) {
                let [a, aDividers] = makeProduct(_a[0], _a[1], _nA);
                let [b, bDividers] = makeProduct(_b[0], _b[1], _nA);

                if (a.eq(b)) continue;

                let all = shuffle([...aDividers, ...bDividers]);
                _ansDividers = all;

                let [ans, c, d, e, f]: Decimal[] = [];

                switch (all.length) {
                    default:
                        continue;
                    case 3:
                        [ans, c, d] = all;
                        abc = [a, b, c, d, ans];
                        break;
                    case 4:
                        [ans, c, d, e] = all;
                        [c, d] = c.lt(d) ? 
                            [c.mul(e), d] : 
                            [c, d.mul(e)];
                        abc = [a, b, c, d, ans];
                        break;
                    case 5:
                        [ans, c, d, e, f] = all;
                        [c, d] = c.lt(d) ? [c, d] : [d, c];
                        [e, f] = e.lt(f) ? [e, f] : [f, e];
                        c = c.mul(f);       // min(c, d) * max(e, f)
                        d = d.mul(e);       // max(c, d) * min(e, f)
                        abc = [a, b, c, d, ans];
                        break;
                }

                if (allAreUnique(abc)) break;
            } 
            return abc;
        }

        if (_gameType == "dividers+-") {
            let r = _a;
            let factors: Map<number, number[]> = new Map([
                [2, [2, 7]],
                [3, [3, 7]],
                [5, [5, 7]],
                [6, [2, 3]],
                [7, [7, 11]],
                [9, [7, 8]],
                [10, [2, 5]],
            ]);
            let ans = randFrom([...factors.keys()]);
            let others = factors.get(ans);
            let s = makeProduct(r[0], r[1], others, ans, false)[0];
            let a = rand(r[0], d2n(s.minus(r[0])));
            let b = s.minus(a);
            return n2d([a, b, ans]);
        }

        throw(`Unknown game type '${_gameType}'`);
    }

    function makeAnswers(abc: Decimal[], rightAns: Decimal): Decimal[] {
        if (_gameType === "+-dots") {
            let f1 = pm(n2d(10)).div(_dotsFactor);
            let f2 = pm(n2d(rand(1, 1))).div(_dotsFactor);
            let f3 = pm(n2d(rand(2, 2))).div(_dotsFactor);
            return shuffle([
                rightAns,
                rightAns.plus(f1),
                rightAns.plus(f2),
                rightAns.plus(f3)
            ]);
        }

        if (_gameType === "equ+-") {
            return shuffle([
                rightAns,
                rightAns.plus(pm(10)),
                rightAns.plus(pm(rand(1, 1))),
                rightAns.plus(pm(rand(2, 2)))
            ]);
        }

        if (_gameType === "equ*/") {
            return shuffle([
                rightAns,
                rightAns.plus(pm(rand(1, 1))),
                rightAns.plus(pm(rand(2, 2))),
                rightAns.plus(pm(rand(3, 3)))
            ]);
        }

        if (_gameType === "ab+c=d" || 
            _gameType === "a*/(b+c)=d" ||
            _gameType === "a/b=c/d") {
            return shuffle([
                rightAns,
                rightAns.plus(pm(rand(1, 1))),
                rightAns.plus(pm(rand(2, 2))),
                rightAns.plus(pm(rand(3, 3)))
            ]);
        }

        if (_gameType.includes("%")) {
            return shuffle([
                rightAns,
                rightAns.plus(pm(5)),
                rightAns.plus(pm(10)),
                rightAns.plus(pm(15))
            ]);
        }

        if (_gameType.includes('fr+-') || _gameType === "fr*/") {
            let variate = (x: Decimal, v: Decimal) => 
                x.gt(v) ? x.plus(v.mul(pm(1))) : x.plus(v);

            for (let loopLimit = 100; true; )
            {
                let answers = [
                    rightAns,
                    variate(rightAns, abc[0]),
                    variate(rightAns, abc[1]),
                    variate(rightAns, abc[2])
                ];

                if (!allAreUnique(answers) && loopLimit-- > 0)
                    continue;

                return shuffle(answers);
            }
        }

        if (_gameType === "fr-reduction") {
            let va = (x: number, v: number) => 
                x > v ? x + pm(v) : x + v;

            let fr = d2f(rightAns);

            for (let loopLimit = 100; true; )
            {
                let n1 = fr.n;
                let n2 = va(fr.n, 1);
                let d1 = fr.d;
                let d2 = va(fr.d, 1);

                let answers = [
                    rightAns,
                    n2d(n2).div(d1),
                    n2d(n1).div(d2),
                    n2d(n2).div(d2)
                ];

                if (!allAreUnique(answers) && loopLimit-- > 0)
                    continue;

                return shuffle(answers);
            }
        }

        if (_gameType === "dividers") {

            let nDividers = _nA.filter(x => !_ansDividers.some(y => x.eq(y)));
            let pAll = _ansDividers.reduce((x, y) => x.mul(y));

            [2, 3].forEach(x => {
                let p = n2d(x);
                while (pAll.mod(p).eq(0)) p = p.mul(x);
                nDividers.push(p);
            });

            // console.log(`choose dividers (${pAll}): `, d2n(_ansDividers), d2n(nDividers));

            let variate = () => {
                for (let loopLimit = 30; true; )
                {
                    let p = randFrom(_ansDividers);

                    if (_ansDividers.length > 2 && trueFalse())
                        p = p.mul(randFrom(_ansDividers));

                    p = p.mul(randFrom(nDividers));

                    if (p.gt(_c[1]) && loopLimit-- > 0)
                        continue;

                    return p;
                }
            }

            for (let loopLimit = 100; true; )
            {
                let answers = [
                    rightAns,
                    variate(),
                    variate(),
                    variate()
                ];

                if (!allAreUnique(answers) && loopLimit-- > 0)
                    continue;

                return shuffle(answers);
            }
        }

        if (_gameType === "dividers-2") {
            let dividers = _ansDividers.filter(x => !x.eq(rightAns));
            dividers = [...dividers, ...n2d([23, 29])];
            let answers: Decimal[];
            for (let loopLimit = 30; loopLimit-- > 0; ) {
                let [a1, a2, a3] = shuffle(dividers);
                answers = [rightAns, a1, a2, a3];

                if (allAreUnique(answers)) break;
            }

            return shuffle(answers);
        }

        if (_gameType === "dividers+-") {
            let answers = [rightAns];
            let s = abc[0].plus(abc[1]);
            for (let f = 2; f < d2n(s); f++) {
                if (rightAns.eq(f)) continue;
                if (s.mod(f).eq(0)) continue;
                if (f === 4) continue;

                answers.push(n2d(f));
                if (answers.length >= 4) break;
            }
            answers = [...answers, ...n2d([11, 13, 17])].slice(0, 4);
            return shuffle(answers);
        }

        throw(`Unknown game type '${_gameType}'`);
    }

    function makeEqu(pattern: string, abc: Decimal[]): [string, Decimal] {

        let vars = pattern.match(/\b[A-Z]\b/g);     // single upcase letters
        let v = randFrom(vars);

        let equ = pattern;

        [
            [/_r/g, "color(red)"],
            [/_g/g, "color(green)"],
            [/_b/g, "color(blue)"]
        ].forEach(g => equ = equ.replace(g[0], <string> g[1]))

        equ = equ
            .replace(v, '$')
            .replace("xx", '@')

        let ans = abc[v.charCodeAt(0) - 'A'.charCodeAt(0)];

        if (_gameType == "ab+c=d") {
            let c = abc[2];
            if (c.lt(0)) {
                abc[2] = c.neg();
                equ = equ.replace('+', "-");
            }
            if (ans.lt(0)) 
                ans = ans.neg();
        }

        if (_gameType === "dividers+-" && trueFalse()) {
            let s = abc[0].plus(abc[1].mul(2));
            abc = [s, abc[1], abc[2], abc[3]];
            equ = equ.replace('+', '-');
        }

        let dotsOnly = _gameType === "+-dots";

        function at(i: number): string {
            if (i >= abc.length) return "";
            if (!abc[i]) return "";
            return dotsOnly ?
                abc[i].toString() :
                d2t(abc[i]);
        }

        equ = equ
            .toLocaleLowerCase()
            .replace(/\ba\b/, at(0))
            .replace(/\bb\b/, at(1))
            .replace(/\bc\b/, at(2))
            .replace(/\bd\b/, at(3))
            .replace(/\be\b/, at(4))
            .replace(/\bf\b/, at(5))
            .replace(/\bg\b/, at(6))
            .replace(/\bh\b/, at(7));

        equ = equ
            .replace('@', "xx")
            .replace('$', randFrom(_x));

        return [equ, ans];
    }

    class CGame implements IGame {
        constructor() {
            for (let cmd of Anki.commands()) {
                switch (cmd[0].toLocaleLowerCase()) {
                    case "equ+-":       _gameType = "equ+-";     break;
                    case "equ*/":       _gameType = "equ*/";     break;
                    case "ab+c=d":      _gameType = "ab+c=d";    break;
                    case "%-simple":    _gameType = "%-simple";  break;
                    case "%-plus":      _gameType = "%-plus";    break;
                    case "%-minus":     _gameType = "%-minus";   break;
                    case "%-all":       _gameType = "%-all";     break;
                    case "game":        _gameType = <GameType> cmd[1];
                    case "no-overflow": _noOverflow = false;     break;
                    case "proper-only": _properOnly = true;      break;
                    case "timer":       _seconds = parseInt(cmd[1]);      break;
                    case "m-scale":     _mathScale = parseFloat(cmd[1]); break;
                    case "no-ab-swap":  _noAbSwap = true;        break;
                    case "dots-factor": _dotsFactor = parseInt(cmd[1]); break;
                    case "x":           _x = cmd.slice(1);       break;
                    case "a": _a = cmd.slice(1).map(x => parseInt(x)); break;
                    case "b": _b = cmd.slice(1).map(x => parseInt(x)); break;
                    case "c": _c = cmd.slice(1).map(x => parseInt(x)); break;
                    case "d": _d = cmd.slice(1).map(x => parseInt(x)); break;
                    case "n": _n = cmd.slice(1).map(x => parseInt(x)); break;
                    case "a[]": _aA = cmd.slice(1).map(x => parseDecimal(x)); break;
                    case "b[]": _bA = cmd.slice(1).map(x => parseDecimal(x)); break;
                    case "c[]": _cA = cmd.slice(1).map(x => parseDecimal(x)); break;
                    case "d[]": _dA = cmd.slice(1).map(x => parseDecimal(x)); break;
                    case "n[]": _nA = cmd.slice(1).map(x => parseDecimal(x)); break;
                }
            }
            _patterns = Anki.tokens();
            _next = -1;
        }

        isLast() {
            return _next + 1 >= _totalRounds;
        }

        addRounds(count: number) {
            _totalRounds += count;
        }

        nextRound(): Round {
            _next++;

            let gameType = _gameType;

            let pattern = _patterns[_next % _patterns.length];
            let abc = newAbc(pattern);

            let [equ, rightAns] = makeEqu(pattern, abc);

            let answers = makeAnswers(abc, rightAns);

            let progress = [
                _next < _totalRounds ? _next : _totalRounds,
                _totalRounds
            ];

            return {
                seconds: _seconds,
                gameType,
                progress,
                equ,
                mathScale: _mathScale,
                rightAns,
                answers
            };
        }
    }

    export function newEquation(): IGame {
        return new CGame();
    }

</script>