<script lang="ts">

    import { pulse } from '../core/Pulse.svelte';

    import {fire, on} from './Model.svelte'
    import type {GcdLcm} from './GcdLcm.svelte'

    export let ga: string;
    export let sz: string = "30px";

    type Model = typeof GcdLcm.game.divisors[0];

    let _: Model = {
        factor: 2,
        mxPow: 3,
        value: 1,
        wrong: false
    }

    let value: number;
    let wrong: boolean = false;
    let pow: number = 0;

    $: bgColor = wrong ? "bg-red-100" :
        pow > 0 ? "bg-green-100" : "bg-gray-100";

    function onClick() {
        if (pow++ == _.mxPow) pow = 0;
        _.value = _.factor ** pow;

        fire("--evt-acted");
        update();
    }

    export function update() {
        value = _.value;
        wrong = _.wrong;
    }

    export function setModel(model: Model) {
        _ = model;
    }

    on("--cmd-update-views", () => update());

</script>

<button
    class="{bgColor}"
    style="grid-area:{ga};font-size:{sz}" 
    on:click={onClick}
    use:pulse
>
    {pow > 0 ? value : _.factor}
</button>
