<script lang="ts">

    import { pulse } from '../core/Pulse.svelte';
    import { getBgColor } from '../core/Grid.svelte';
    import { bounceOnDrag, soundsOnDrag } from "../core/OnDrag.svelte";

    import {on, fire,} from './Model.svelte'
    import type {Game} from './Model.svelte'

    export let ga: string;
    export let index: number;
    export let sz: number = 30

    let _num: string;
    let _bgColor = getBgColor(ga);
    let _scale: number
    let _sz: number;

    function onClick() {
        fire("--evt-click");
    }

    function updateView(game: Game) {
        _num = game.nums[index].toLocaleString('en');
        _scale = _num.length <= 3 ? 1.2 : 2.0;
        _sz = sz - _scale * _num.length;
    }

    on("--cmd-update-views", updateView);

</script>

<button
    class="{_bgColor}"
    style="grid-area:{ga};font-size:{_sz}px" 
    on:click={onClick}
    use:pulse
    use:bounceOnDrag
    use:soundsOnDrag={new Map([
        ["drag:start", "yes"],
        ["drag:end", "no"],
    ])}
>
    {_num}
</button>
