<script lang="ts">
    import { versions } from "./Version.Store";

    export let ga: string;
    export let v: string;

    versions.add(v, true);

    let {current} = versions;
</script>

<!-- svelte-ignore a11y-click-events-have-key-events -->
<span 
    on:click={() => versions.next()}
    class="text-xs text-gray-300"
    style="grid-area:{ga}"
>
   {$current}
</span>