import "../core/extensions/all.extensions";
import { WithIndex } from "./Extensions";
import { fixCrInCells } from "./Helpers";
import { n2d, t2d, isArrayIndex } from "./Helpers.convert";
import { throwError, _l } from "./Logger";
export function newArgsArray(grid) {
    let error = (fail, msg) => fail ? throwError(msg, x => _l(x)) : undefined;
    let _arr = grid.flat();
    function get(indx, fail) {
        return _arr[indx];
    }
    function rands(count) {
        return [n2d(0)];
    }
    return WithIndex((indx) => get(indx, true), {
        count: _arr.length,
        all: () => _arr,
        get: (indx, fail = true) => get(indx, fail),
        has: (indx) => get(indx, false) !== undefined,
        v2i: (indx, fail = true) => {
            let v = get(indx, true);
            return isArrayIndex(v) ? Number(v) : error(fail, `Not an index '${v}'`);
        },
        v2d: (indx, fail = true) => t2d(get(indx, fail)),
        rand: () => rands(1)[0],
        rands: (count = 1) => rands(count),
    });
}
export function parseArgsArray(str) {
    if (str.trim() === "") {
        return newArgsArray([]);
    }
    str = fixCrInCells(str, ';');
    let grid = str
        .splitTrimNoEmpties('\n')
        .map(ln => ln.splitTrim('\t'));
    return newArgsArray(grid);
}
