<script context="module" lang="ts">

    import { beingDragged } from "./DragHandler.svelte";
    import { delay, run } from "./Helpers";
    import { randFrom, rand } from "./Helpers.rand";

    let Config = {
        duration: 500,
        interval: 10000,
        jitter: 7000,
    }

    let zoomOut = [
        { transform: 'scale(1, 1)' },
        { transform: 'scale(1.03, 1.03)' },
        { transform: 'scale(1, 1)' }
    ];
    
    let zoomIn = [
        { transform: 'scale(1, 1)' },
        { transform: 'scale(0.98, 0.98)' },
        { transform: 'scale(1, 1)' }
    ];

    let xShift = [
        { transform: 'translate(0px, 0px)' },
        { transform: 'translate(-1px, 0px)' },
        { transform: 'translate(+1px, 0px)' },
        { transform: 'translate(0px, 0px)' }
    ];
    
    let yShift = [
        { transform: 'translate(0px, 0px)' },
        { transform: 'translate(0px, -1px)' },
        { transform: 'translate(0px, +1px)' },
        { transform: 'translate(0px, 0px)' }
    ];
    
    let xyShift = [
        { transform: 'translate(0px, 0px)' },
        { transform: 'translate(-1px, -1px)' },
        { transform: 'translate(+1px, +1px)' },
        { transform: 'translate(0px, 0px)' }
    ];
    
    let yxShift = [
        { transform: 'translate(0px, 0px)' },
        { transform: 'translate(+1px, -1px)' },
        { transform: 'translate(-1px, +1px)' },
        { transform: 'translate(0px, 0px)' }
    ];
    
    let rotate = [
        { transform: 'rotate(0deg)' },
        { transform: 'rotate(-2deg)' },
        { transform: 'rotate(+2deg)' },
        { transform: 'rotate(0deg)' }
    ];

    let pulses = [
        zoomOut, zoomIn,
        xShift, yShift, xyShift, yxShift,
        rotate
    ];

    async function startPulsing(elem: HTMLElement, config: typeof Config) {
        for (;;) {
            let {interval, jitter, duration} = config;
            let timeout = rand(interval - jitter, interval + jitter);

            await delay(timeout);

            if (beingDragged(elem))
                continue;

            let pulse = randFrom(pulses);
            elem.animate(pulse, duration);
        }
    }

    export function pulse(elem: HTMLElement, config?: typeof Config) {
        run(() => startPulsing(elem, config ?? Config));
    }

</script>