import { readable } from "svelte/store";
export function makeProgress({ startFrom, doneCount, onDone }) {
    let _current = startFrom ?? 0;
    let _max = doneCount;
    function nop(_) { }
    ;
    let _setCurrent = nop;
    let _setMax = nop;
    let value = readable(_current, set => {
        _setCurrent = set;
        return () => _setCurrent = nop;
    });
    let max = readable(_max, set => {
        _setMax = set;
        return () => _setMax = nop;
    });
    let isOn = readable(doneCount > 1);
    function fireIfDone() {
        if (_current === _max)
            onDone();
    }
    function rightClick() {
        if (_current === _max) {
            _setMax(++_max);
        }
        _setCurrent(++_current);
        fireIfDone();
    }
    function wrongClick() {
        _setMax(_max += 1);
        fireIfDone();
    }
    function addWork(count = 1) {
        _setMax(_max += count);
        fireIfDone();
    }
    return {
        value,
        max,
        isOn,
        rightClick,
        wrongClick,
        addWork
    };
}
