<script context="module" lang="ts">

    import {onAny, fireAny} from "./../core/IoBus.svelte"
    import {CTimedCmd} from "./../core/TimedCmd.svelte"
    import type {ITimedCmd} from "./../core/TimedCmd.svelte"
    
    export interface Game {
        roundsLeft: number,
        nums: number[],
        divisors: {
            factor: number;
            mxPow: number;
            value?: number;
            wrong?: boolean;
        }[];
    }

    export interface Round {
        nums: number[];
        divisors: number[];
        gcd: number[];
        lcm: number[];
    }

    export interface Msgs {
        "--cmd-set-models": Game,
        "--cmd-new-round": {},
        "--cmd-update-views": {},
        "--evt-acted": {},
        "--evt-go": {},
        "--evt-done": {}
        "--cmd-idle": {}
    }

    export function on<Name extends keyof(Msgs)>(name: Name, cb: (args: Msgs[Name]) => void): void {
        onAny(name, cb);
    }

    export function fire<Name extends keyof(Msgs)>(name: Name, args?: Msgs[Name]): void {
        fireAny(name, args);
    }

    export function NewTimedCmd(cmd: keyof Msgs, timeout: number, jitter?: number): ITimedCmd {
        return new CTimedCmd(cmd, timeout, jitter ?? 0);
    }

</script>