<script lang="ts">

    import { onDestroy, onMount } from 'svelte';

    import { getBgColor } from './Grid.svelte';

    export let ga: string;
    export let seconds: number;
    export let onExpiration: () => void;
    export let color: string = "bg-red-500";
    export let bit: number = 30;

    let _startedAt: number;
    let _deadLine: number;
    let _bar: number = 0;

    let _bgColor = getBgColor(ga);
    let _intervalId: any;

    function stopCounting() {
        clearInterval(_intervalId);
    }

    function startCounting() {
        function tick() {
            let now = Date.now();
            _bar = 100 * ((now - _startedAt) / (1000*seconds));

            if (now >= _deadLine) {
                onExpiration();
                stopCounting();
            }
        }

        _startedAt = Date.now();
        _deadLine = _startedAt + 1000 * seconds;

        _intervalId = setInterval(() => tick(), bit);
    }

    onMount(() => startCounting());
    onDestroy(() => stopCounting());

    export function restart() {
        startCounting();
    }

</script>

<div 
    class="w-full h-full {_bgColor}"
    style="grid-area:{ga}" 
>
    <div 
        class= "h-full w-full {color}"
        style="width:{_bar}%"
    >
    </div>
</div>
