<script context="module" lang="ts">

    import {rand, randFrom, shuffle, trueFalse, randSign} from "../core/Helpers.rand"
    import {Anki} from './../core/Anki';
    import {Mp3} from "./../core/Mp3.svelte";
    import {log, _l} from "../core/Logger";

    import {newTimedCmd} from "./Model.svelte"
    import {fire,} from './Model.svelte'
    import type {Config, Game, Msgs, Round} from './Model.svelte'

    interface IPlusOver {
        newRound();
        gotClicked();
        gotAnswer(args: Msgs["--evt-answer"]);
        yes();
        no();
        done();
        idle();
    }

    let _game: Game;
    let _tcNewRound = newTimedCmd("--cmd-new-round", 1000);
    let _tcIdle = newTimedCmd("--cmd-idle", 10000, 3000);

    let _playbook: Round[] = [];
    let _roundNo: number = -1;
    let _m: number[] = [1, 8];

    function getPair(): number[] {
        let nums = Anki.tokens();
        let pairs = [];
        for (let i = 0; i < nums.length - 1; i += 2) {
            pairs.push([
                parseInt(nums[i]),
                parseInt(nums[i+1])
            ]);
        }
        let pair = randFrom(pairs);
        return pair;
    }

    function splitToLines(numsInLine: number): number[][] {
        let lines = [];
        let line = [];

        for (let token of Anki.tokens()) {
            let num = parseInt(token);
            line.push(num);

            if (line.length == numsInLine) {
                lines.push(line);
                line = [];
            }
        }

        return lines;
    }

    function getLine(): number[] {
        return randFrom(splitToLines(4));
    }

    function addPlusRound() {
        let pair = getPair();

        let sumDigit = (pair[0] + pair[1]) % 10;

        let op = '+';

        let a0 = pair[0];
        let a1 = rand(_m[0], _m[1]);
        let a = 10*a1 + a0;

        let b0 = pair[1];
        let b1 = rand(_m[0], _m[1] + 1 - a1);
        let b = 10*b1 + b0;

        let ans = a + b;

        let nums = shuffle([a, b]);

        let answers = shuffle([
            ans,
            ans + randSign() * rand(1, 9),
            ans > 50 ? ans - 10 : ans + 10
        ]);

        let rightAt = answers.findIndex(x => x == ans);

        _playbook.push({
            pair,
            nums,
            op,
            sumDigit,
            answers,
            rightAt
        });
    }

    function addMinusRound() {
        let pair = getPair();
        let sumDigit = (pair[0] + pair[1]) % 10;

        let op = '-';

        let a0 = pair[0];
        let a1 = rand(_m[0], _m[1] + 1);
        let a = 10*a1 + a0;

        let b1 = rand(_m[0], _m[1] + 1 - a1);
        let b0 = pair[1];
        let b = 10*b1 + b0;

        let sum = a + b;

        let nums = [sum, trueFalse() ? a : b];
        let ans = sum - nums[1];

        let answers = shuffle([
            ans, 
            ans + rand(1, 9) * randSign(),
            ans > 50 ? ans - 10 : ans + 10
        ]);

        let rightAt = answers.findIndex(x => x == ans);

        _playbook.push({
            pair,
            nums,
            op,
            sumDigit,
            answers,
            rightAt
        });
    }

    function addMulRound() {
        let line = getLine();

        let a = rand(line[0], line[1]);
        let b = rand(line[2], line[3]);

        let pair = [a % 10, b % 10];
        let nums = shuffle([a, b]);
        let op = "x";

        let ans = a * b;
        let sumDigit = ans % 10;

        let answers = shuffle([
            ans,
            ans + randSign() * Math.min(...nums),
            ans > 50 ? ans - 10 : ans + 10
        ]);

        let rightAt = answers.findIndex(x => x == ans);

        _playbook.push({
            pair,
            nums,
            op,
            sumDigit,
            answers,
            rightAt
        });
    }

    function addDivRound() {
        let line = getLine();

        let a = rand(line[0], line[1]);
        let b = rand(line[2], line[3]);

        let prod = a * b;

        let pair = [a % 10, b % 10];
        let nums = [prod, randFrom([a, b])];
        let op = '/';

        let ans = prod / nums[1];
        let sumDigit = (a * b) % 10;

        let answers = shuffle([
            ans, 
            ans > 2 ? ans + randSign() : ans + 1,
            ans > 50 ? ans - 10 : ans + 10
        ]);

        let rightAt = answers.findIndex(x => x == ans);

        _playbook.push({
            pair,
            nums,
            op,
            sumDigit,
            answers,
            rightAt
        });
    }

    function addNextRounds() {
        let config = Anki.getConfig<Config>();
        switch (config.game.toLocaleLowerCase()) {
            case "plus-over":
                addPlusRound();
                addMinusRound();
                break;
            case "mul-div":
                addMulRound();
                addDivRound();
                break;
        }
    }

    function fillRoundsAtStart() {
        let config = Anki.getConfig<Config>();
        let noOfRounds = config.rounds ?? 4;

        for (let cnt = noOfRounds; cnt-- > 0; )
        {
            addNextRounds();
        }
    }

    class CPlusOver implements IPlusOver {

        constructor() {
            for (let cmd of Anki.commands()) {
                switch (cmd[0].toLocaleLowerCase()) {
                    case "m": _m = cmd.slice(1).map(x => parseInt(x)); break;
                }
            }
        }

        newRound() {
            let config = Anki.getConfig<Config>();

            _tcIdle.reset();
            _tcNewRound.cancel();

            _roundNo++;

            if (_roundNo >= _playbook.length)
                log.error(`Round index out of bound: ${_roundNo} length=${_playbook.length}`, x => _l(x));

            let round = _playbook[_roundNo];
            let count = _playbook.length - _roundNo;

            let showHint = config.showHint ?? true;

            _game = {
                roundsLeft: count,
                clickedAt: 0,
                state: "first-click",
                showHint,
                ...round
            }

            fire("--cmd-update-views", _game);
        }

        gotClicked() {
            switch (_game.state) {
                case "first-click": 
                    fire("--evt-answer");
                    break;
                case "answered": 
                    fire("--cmd-new-round");
                    break;
            }
        }

        yesNo() {
            if (_game.state !== "answered")
                return;

            if (_game.roundsLeft <= 0)
                return;

            let correct = _game.clickedAt === _game.rightAt;

            fire(correct ? "--evt-yes" : "--evt-no");
        }

        gotAnswer(args: Msgs["--evt-answer"]) {
            _tcIdle.reset();

            switch (_game.state) {
                case "first-click":
                    _game.state = "active";
                    break;
                case "active":
                    _game.clickedAt = args.index;
                    _game.state = "answered";
                    this.yesNo();
                    break;
                case "answered":
                    fire("--cmd-new-round");
                    return;
            }

            fire("--cmd-update-views", _game);
        }

        yes() {
            if (_game.roundsLeft === 1) {
                fire("--evt-done");
            } else {
                Mp3.play("yes");
            }

            _tcNewRound.reset();
        }

        no() {
            Mp3.play("no");
            addNextRounds();
        }

        done() {
            Mp3.play("done");
            addNextRounds();
        }

        idle() {
            _tcIdle.reset();
            Mp3.play("idle");
        }
    }

    export const PlusOver: IPlusOver = new CPlusOver();

    fillRoundsAtStart();

</script>
