<script lang="ts">

    import {onMount} from 'svelte';

    import Oops from "./core/Oops.svelte"
    import Logger from "./core/Logger.svelte"
    import Grid, { getBgColor } from "./core/Grid.svelte"
    import Version from "./core/Version.svelte"
    import Powered from './core/Powered.svelte';
    import {log, _l} from "./core/Logger"
    import {Anki} from './core/Anki'
    import {initMath} from "./core/MathJax.svelte";
    import {Mp3} from './core/Mp3.svelte';

    import PlusOver from "./plus-over/Main.svelte"
    import GcdLcm from "./gcd-lcm/Main.svelte"
    import Zero from "./zero/Main.svelte"
    import Equ from "./equation/Main.svelte"
    import FrPair from "./fr-pairs/Main.svelte"
    import WProblem from "./w-problem/WProblem.View.svelte"
    import Sequence from "./sequence/Sq.View.svelte"

    onMount(() => {
        Anki.hideLoader();
        initMath();
    })

    interface Config {
        game: string,
        rounds: number,
        showHint: boolean,
        soundOn: boolean
    }

    function loadGame() {
        try
        {
            let config = Anki.getConfig<Config>();
            Mp3.buildMp3Library("assets", config.soundOn);

            switch (config.game) {
                case "plus-over":
                case "mul-div":
                    return PlusOver;
                case "gcd-lcm":
                    return GcdLcm;
                case "zero":
                    return Zero;
                case "equ-abc":
                    return Equ;
                case "fr-pairs":
                    return FrPair;
                case "w-problem":
                    return WProblem;
                case "sequence":
                    return Sequence;
                default:
                    log.error(`Unknown game type: '${config.game}'`, x => _l(x));
                    return Oops;
            }
        }
        catch (err)
        {
            log.error("Failed to loadGame", err, x => _l(x));
        }
    }

    let layout = {
        areas: `
            "game  game  game  game"
            "pwrd  pwrd  .     ver"
        `,
        cols: `1fr 1fr 1ft 1fr`,
        rows: `98fr 2fr`,
    };


</script>

<Grid {layout}>
    <div 
        class="w-full h-full"
        style="grid-area:game"
    >
        <svelte:component this={loadGame()}/>
    </div>
    <Powered ga="pwrd" text="Powered by gebemot.online"/>
    <Version ga="ver" v="0.51.4"/>
</Grid>

<Logger/>
