<script context="module" lang="ts">

    import { getContext, setContext } from "svelte";

    export function getBgColor(ga: string, $default?: string) {
        let fn: any = getContext("get-bg-color");
        if (!fn) return $default ?? "bg-transparent";
        let bgColor = fn(ga, $default);
        // console.log(`${ga}: ${bgColor}`);
        return bgColor;
    }

</script>

<script lang="ts">

    interface Layout {
        areas: string;
        cols: string;
        rows: string;
        bgColors?: any;
    }

    export let layout: Layout;
    export let ga: string = "unset";
    export let bgColor: string = getBgColor(ga);
    export let clazz: string = "";
    export let style: string = "";

    setContext("get-bg-color", function(ga: string, $default?: string) {
        if (!layout.bgColors) return  $default ?? "bg-transparent";
        return layout.bgColors[ga] ?? $default ?? "bg-transparent";
    })

</script>

<!-- svelte-ignore a11y-click-events-have-key-events -->
<div 
    class="my-grid w-full h-full {bgColor} {clazz}"
    on:click|self
    style="
        grid-area:{ga};
        --areas: {layout.areas}; 
        --cols: {layout.cols};
        --rows: {layout.rows};
        {style}
    "
>
    <slot></slot>
</div>


<style>
    .my-grid {
        width: 100%;
        height: 100%;
        display: grid;
        align-items: center;
        justify-items: center;
        grid-template-areas: var(--areas);
        grid-template-columns: var(--cols);
        grid-template-rows: var(--rows);
    }
</style>