import { formatG2l } from "./g2l-formatter";
export const meter_sm = (value, fmt) => formatG2l(value, fmt, {
    gu: 'מטר',
    lu: 'ס"מ',
    g2l: x => x.mul(100),
    luLimit: 1000
});
export const km_meter = (value, fmt) => formatG2l(value, fmt, {
    gu: 'ק"מ',
    lu: 'מטר',
    g2l: x => x.mul(1000),
    luLimit: 5000
});
export const sm_mm = (value, fmt) => formatG2l(value, fmt, {
    gu: 'ס"מ',
    lu: 'מ"מ',
    g2l: x => x.mul(10),
    luLimit: 100
});
