import "../core/extensions/all.extensions";
import { parseArgsArray } from "../core/ArgsArray";
import { parseArgsGrid } from "../core/ArgsGrid";
import { Config } from "./Sq.Config";
let valueOf = (str) => str ? str.trim() : "";
function parseViewTemplate(str) {
    return str
        .split('\n')
        .map(ln => ln.splitTrim('\t'));
}
export function makeSqDefinition(card = null) {
    card ??= ankiCard;
    let problemId = valueOf(card.problemId);
    let content = parseArgsGrid(valueOf(card.content));
    let viewTemplate = parseViewTemplate(valueOf(card.view));
    let answers = parseArgsArray(valueOf(card.answers));
    let options = parseArgsGrid(valueOf(card.options));
    let nCount = options.v2i("count") ??
        Config.nCountInSequence;
    let rounds = options.v2i("rounds") ?? 1;
    let timer = options.v2i("timer") ?? 0;
    return {
        problemId,
        content,
        nCount,
        viewTemplate,
        answers,
        options,
        settings: {
            rounds,
            timer
        },
    };
}
