import { log, _l } from "../core/Logger";
function parseText(str) {
    str = str.replaceAll("\r", "\n");
    let lines = str
        .replaceAll("U+", "&#x")
        .replaceAll("\r", "\n")
        .splitTrimNoEmpties("\n");
    let text = [];
    let css = "";
    for (let line of lines) {
        if (line.startsWith("{")) {
            if (!line.endsWith("}"))
                continue;
            css = line.slice(1, -1);
            continue;
        }
        text.push({ line, css });
        css = "";
    }
    return text;
}
function parseValues(str) {
    if (!str)
        return [];
    let tokens = str.includes('\t') ? str.split('\t') :
        str.includes('\n') ? str.split('\n') :
            str.includes(';') ? str.split(';') :
                str.includes(',') ? str.split(',') :
                    str.split(' ');
    return tokens.map(s => s.trim());
}
function parseAnswers(str) {
    let answers = parseValues(str);
    return answers;
}
function parseVariables(str, sets) {
    let defines = parseValues(str);
    let vars = defines.map((def, indx) => {
        let [name, fmt] = def.split(':');
        let setOfInvariantValues = new Set();
        if (name.startsWith('~')) {
            name = name.substring(1);
            setOfInvariantValues = new Set(sets.map(set => set[indx]));
        }
        return {
            name,
            fmt: fmt ?? "",
            invariantValues: [...setOfInvariantValues]
        };
    });
    return vars;
}
function parseSets(str) {
    let lines = str.includes("<br>") ? str.split("<br>") :
        str.includes('\n') ? str.split('\n') :
            str.includes('\r') ? str.split('\r') :
                str.includes(';') ? str.split(';') :
                    [str];
    lines = lines
        .map(ln => ln.trim())
        .filter(ln => ln != "");
    let sets = lines
        .map(ln => parseValues(ln));
    return sets;
}
function parseOptions(card) {
    let options = parseValues(valueOf(card.options))
        .map(o => o.includes('=') ? o.split("=") : [o, ""]);
    let hebTest = false;
    let helpMeTimeout = parseInt(card.helpMeTimeout);
    if (isNaN(helpMeTimeout) || helpMeTimeout <= 0)
        helpMeTimeout = 60 * 60 * 1000;
    options.forEach(o => {
        switch (o[0]) {
            case "heb-test":
                hebTest = true;
                break;
            default:
                log.error(`Unknown option '${o}'`, x => _l(x));
                break;
        }
    });
    return {
        hebTest,
        helpMeTimeout
    };
}
let valueOf = (str) => str ? str.trim() : str;
let newWProblemTemplate = (card) => {
    let sets = parseSets(valueOf(card.sets));
    let problemId = valueOf(card.problemId);
    let text = parseText(valueOf(card.content));
    let answers = parseAnswers(valueOf(card.answers));
    let variables = parseVariables(valueOf(card.variables), sets);
    let options = parseOptions(card);
    return {
        problemId,
        text,
        answers,
        variables,
        sets,
        options
    };
};
export default newWProblemTemplate;
