<script lang="ts">
    import type { Decimal } from "decimal.js"

    import { toAscii } from "../core/AsciiMathUtils";
    import { isFraction, d2t } from "../core/Helpers.convert"
    import { trueFalse } from "../core/Helpers.rand";
    import { pulse } from '../core/Pulse.svelte';
    import { getBgColor } from '../core/Grid.svelte';
    import { bounceOnDrag } from "../core/OnDrag.svelte"
    import type { GameType } from "./Model.svelte";

    type State = "start" | "clicked" | "right" | "wrong";

    export let gameType: GameType;
    export let ga: string;
    export let value: Decimal;
    export let units: string = "";
    export let bgColor = getBgColor(ga, "bg-gray-100");
    export let sz: number = 30;

    let _text: string;
    let _sz: number;
    let _bgColor: string;

    function toText(v: Decimal) {
        if (!isFraction(v) && !units)
            return v.toNumber().toLocaleString('en');

        let dotsOnly = gameType === "+-dots";

        let text = dotsOnly ?
            v.toString() :
            d2t(v, trueFalse());

        if (units)
            text += units;

        return toAscii(text);
    }

    export function setState(state: State) {
        switch (state) {

            case "start":
                _text = "??";
                _bgColor = bgColor;
                break;

            case "clicked":
                _text = toText(value);
                _bgColor = bgColor;
                break;

            case "right":
                _text = toText(value);
                _bgColor = "bg-green-200";
                break;

            case "wrong":
                _text = toText(value);
                _bgColor = "bg-red-200";
                break;
        }

        _sz = sz - 1.2 * _text.length;
    }

</script>


<button
    class="{_bgColor}"
    style="grid-area:{ga};font-size:{_sz}px" 
    use:pulse
    use:bounceOnDrag
    on:click
>
    {_text}
</button>
