<script lang="ts">

    import { pulse } from '../core/Pulse.svelte';
    import { getBgColor } from '../core/Grid.svelte';
    import { bounceOnDrag } from "../core/OnDrag.svelte"

    type State = "start" | "right" | "wrong";

    export let ga: string;
    export let value: string;
    export let bgColor = getBgColor(ga, "bg-gray-100");
    export let sz: number = 24;

    let _bgColor: string = bgColor;
    $: _sz = sz - 1.1 * value.length;

    export function setState(state: State) {
        switch (state) {

            case "start":
                _bgColor = bgColor;
                break;

            case "right":
                _bgColor = "bg-green-200";
                break;

            case "wrong":
                _bgColor = "bg-red-200";
                break;
        }
    }

</script>

<button
    class="{_bgColor} py-0.5 my-1"
    style="grid-area:{ga};font-size:{_sz}px" 
    use:pulse
    use:bounceOnDrag
    on:click
>
    `{value}`
</button>