<script lang="ts">

    import Grid from './../core/Grid.svelte'
    import Clock from './../core/Clock.svelte'
    import Congrats from '../core/Congrats.svelte';

    import {PlusOver as self} from './PlusOver.svelte'
    import {on, fire,} from './Model.svelte'
    import Count from './Count.svelte'
    import Num from './Num.svelte'
    import Op from './Op.svelte'
    import Ans from './Ans.svelte'
    import Pair from './Pair.svelte'
    import Digit from './Digit.svelte'

    let congrats: Congrats;

    function done() {
        congrats.play();
        self.done();
    }

    on("--cmd-new-round", () => self.newRound());
    on("--evt-answer", args => self.gotAnswer(args));
    on("--evt-click", () => self.gotClicked());
    on("--evt-done", () => done());
    on("--evt-yes", () => self.yes());
    on("--evt-no", () => self.no());
    on("--cmd-idle", () => self.idle());
    fire("--cmd-new-round");

    let layout = {
        areas: `
            "cnt   p1   clk "
            "cnt   p2   clk "
            "lhs   op   rhs "
            "a1    a2   a3  "
        `,
        cols: `1fr 1fr 1fr`,
        rows: `30px 30px 100px 100px`,
        bgColors: {
            cnt: "bg-purple-300",
            p1:  "bg-yellow-100",
            p2:  "bg-pink-100",
            lhs: "bg-green-100",
            op:  "bg-yellow-200",
            rhs: "bg-green-100"
        }
    };

</script>

<Grid {layout}
    on:click={() => fire("--evt-click")}
>
    <Count ga="cnt" />
    <Pair  ga="p1" />
    <Digit ga="p2" />
    <Clock ga="clk" />
    <Num   ga="lhs" index={0} />
    <Op    ga="op" />
    <Num   ga="rhs" index={1} />
    <Ans   ga="a1"  index={0} />
    <Ans   ga="a2"  index={1} />
    <Ans   ga="a3"  index={2} />

    <Congrats bind:this={congrats} />
</Grid>
