import { toAscii } from "../AsciiMathUtils";
import { randFrom } from "../Helpers.rand";
import { log, _l } from "../Logger";
import { km_meter, meter_sm, sm_mm } from "./distance-formatters";
import { kg_gram, kilo } from "./mass-formatters";
import { shekel_agorot } from "./money-formatters";
import { p2, p3, percents } from "./percents-formatters";
import { time, timeWithFractionsOnly, variateTime } from "./time-formatters";
function newFormatters() {
    function toMath(value) {
        return value.includes("/") ? toAscii(value) : value;
    }
    /*
        let tmTest = [
            15,
            30,
            10, 20, 40, 50,
            23,
    
            60,
            75,
            90,
            75, 85, 105,
            83,
    
            120,
            135,
            150,
            135, 145, 165,
            143,
    
            180, 240,
            255,
            270,
            275, 285, 305,
            263,
        ].map(x => variateTime(d2t(n2d(x).div(60))))
        .flat();
    */
    function math(value) {
        return [toAscii(value)];
    }
    let formatters = new Map([
        ["p", percents],
        ["p1", percents],
        ["p2", p2],
        ["p3", p3],
        ["m", kilo],
        ["m1", kilo],
        ["m2", kg_gram],
        ["t", time],
        ["t1", time],
        ["t2", timeWithFractionsOnly],
        ["d", km_meter],
        ["d1", km_meter],
        ["d2", meter_sm],
        ["d3", sm_mm],
        ["a", math],
        ["a1", math],
        ["f", s => [""]],
        ["f1", s => [""]],
        ["$", shekel_agorot],
        ["$1", shekel_agorot],
        ["$2", shekel_agorot],
    ]);
    return {
        apply: (value, fmt) => {
            if (value.includes("`"))
                return value;
            if (!fmt || fmt === "")
                return toMath(value);
            if (formatters.has(fmt)) {
                let variations = formatters.get(fmt)(value, fmt);
                if (variations.length === 0) {
                    log.error(`no formatter variations for '${value}'; fmt='${fmt}'`, x => _l(x));
                    variations.push(value);
                }
                return randFrom(variations);
            }
            log.error(`unknown formatter '${fmt}'`, x => _l(x));
            return toMath(value);
        },
        variateTime: (value) => variateTime(value)
    };
}
export const formatters = newFormatters();
