import { writable } from "svelte/store";
import { makeCell } from "./Sq.Cell";
import { shuffle } from "../core/Helpers.rand";
export function makeSqStore(model) {
    let stateNo = writable(0);
    let view = model.view;
    let options = model.options;
    function makeSheet() {
        let [rows, cols] = [view.length, view[0].length];
        let sheet = [];
        let canBeQuestion = model.content["formula"].includes("lambda") ||
            model.content["formula"].includes("magic-box");
        for (let row = 0; row < rows; row++) {
            let line = [];
            for (let col = 0; col < cols; col++) {
                let value = view[row][col] ?? "";
                line.push(makeCell({
                    row,
                    col,
                    content: value,
                    stateNo,
                    canBeQuestion,
                    options
                }));
            }
            sheet.push(line);
        }
        return sheet;
    }
    let sheet = makeSheet();
    let answers = shuffle(model.answers
        .map(ans => ({
        value: ans.value,
        isRight: ans.isRight,
        isClicked: writable(false)
    })));
    return {
        stateNo,
        view,
        sheet,
        answers,
        options
    };
}
;
