<script lang="ts">

    import { pulse } from '../core/Pulse.svelte';
    import { getBgColor } from '../core/Grid.svelte';

    let _count: number = 1;

    export let ga: string;
    export let sz: string = "20px"

    let _bgColor = getBgColor(ga);

    export function setModel(count: number) {
        _count = count;
    }

</script>

<button 
    class="{_bgColor}"
    style="grid-area:{ga};font-size:{sz}" 
    use:pulse
>
    {_count}
</button>
