<script lang="ts">
    import { fade } from 'svelte/transition';
    import { bounceOnDrag } from '../core/OnDrag.svelte';
    import { pulse } from '../core/Pulse.svelte';

    import type { HelpMe } from "./HelpMe";

    export let helpMe: HelpMe;
    export let text = "הסבר לי בשיעור";

    let bgColor: string = "";

    let {clicked} = helpMe.store;

    $: bgColor = $clicked ? "bg-green-200" : "bg-inherit";

</script>

<div class="container mx-0 min-w-full flex flex-col items-center">
    <button
        class="text-sm {bgColor}"
        transition:fade
        use:pulse
        use:bounceOnDrag
        on:click={() => helpMe.apply()}
    >
        {text}
    </button>
</div>
